/* Shared properties */
:root {
	--gridHeaderHeight: 75px;
}

/* Timeline */
.simple-grid .float-mid {
	width: 100%;
	align-self: end;
	justify-self: start;
}

/* Overlay */
.simple-grid .overlay-container .overlay {
	grid-column: auto;
	grid-row: auto;
}

/* 480x320 phones and above */
@media only screen and (min-width: 320px) {
	/* Content panel */
	.simple-grid .content-panel {
		grid-column: main-start / side-end;
		grid-row: extf-start / foot-end;
	}
	.simple-grid .content-panel.overlay {
		grid-row: head-start / foot-end;
	}

	/* Featured Stories panel */
	.simple-grid .featured-stories-panel {
		grid-column: main-start / side-end;
		grid-row: extf-start / foot-end;
	}

	/* Header */
	.simple-grid header {
		grid-area: head;
		display: grid;
		grid-template-columns: auto minmax(70px, max-content);
		justify-content: space-between;
		align-items: center;
		margin: 0 20px;
		gap: 10px;
	}

	/* Controls */
	.simple-grid .float-right-bottom {
		grid-column: 11 / side-end;
		grid-row: foot-start / foot-end;
		align-self: end;
		justify-self: start;
		width: 100%;
		margin-bottom: 105px;
		padding-right: 20px;
		position: absolute;
	}

	/* Timeline */
	.simple-grid .float-mid {
		grid-column: 2 / side-start;
		grid-row: 3;
	}
	.simple-grid .float-mid.low {
		grid-row: 4;
	}

	/* Time */
	.simple-grid .float-mid-bottom {
		grid-area: foot;
		grid-template-columns: auto max-content;
		grid-template-rows: auto auto;
		align-self: center;
		justify-self: center;
		margin: 0 20px;
		width: calc(100% - 40px);
		display: grid;
		justify-items: start;
		position: absolute;
		gap: 10px 5px;
		bottom: 0;
	}

	/* TODO Add animations */
	.simple-grid .float-mid-bottom.hidden {
		display: none;
	}

	/* Compare */
	.simple-grid .compare {
		display: grid;
		grid-template-rows: 60px calc(var(--vh) / 2 - 60px) calc(var(--vh) / 2 - 40px) 40px;
		grid-template-columns: auto;
		grid-row: head-start / foot-end;
		grid-column: main-start / foot-end;
	}
}

/* Landscape. 480x320 phones and above */
@media only screen and (min-width: 320px) and (orientation: landscape) {
	/* Header */
	.simple-grid header {
		margin: 0;
	}

	/* Controls */
	.simple-grid .float-right-bottom {
		grid-column: 9 / side-end;
		grid-row: foot;
		align-self: end;
		justify-self: end;
		display: grid;
		align-items: end;
		justify-content: end;
		margin-bottom: 0;
		padding-right: 56px;
		padding-bottom: 10px;
	}

	/* Timeline */
	.simple-grid .float-mid {
		grid-row: main;
	}

	/* Time */
	.simple-grid .float-mid-bottom {
		grid-template-columns: auto max-content;
		grid-template-rows: auto auto;
		grid-column: main-start / side-end;
		grid-row: foot-start / foot-end;
		height: auto;
		align-self: start;
		align-items: initial;
		gap: 10px 5px;
	}

	/* Compare */
	.simple-grid .compare {
		display: grid;
		grid-template-rows: 60px auto 40px;
		grid-template-columns: 50% 50%;
		grid-row: head-start / foot-end;
		grid-column: main-start / foot-end;
		width: calc(100% + 40px);
		margin-left: -20px;
	}
}

/* Portrait Tablets */
@media only screen and (min-width: 641px) and (orientation: portrait) {
	/* Controls */
	.simple-grid .float-right-bottom {
		grid-column: 9 / side-end;
		grid-row: foot;
		align-self: end;
		justify-self: end;
		display: grid;
		align-items: end;
		justify-content: end;
		height: 51px;
		margin-bottom: 0;
		padding-right: 56px;
		padding-bottom: 10px;
	}
}

/* Exclude panorama. Big landscape tablets, laptops/desktops, and above */
@media only screen and (min-width: 1025px) and (min-height: 600px) {
	/* Detail panel */
	.simple-grid .detail {
		grid-column: main-start / main-end;
		grid-row: head-start / foot-end;
	}

	/* Content panel */
	.simple-grid .content-panel {
		grid-column: main-start / main-end;
		grid-row: head-start / foot-end;
		display: grid;
		grid-template-rows: 80px max-content auto 3%;
		grid-template-columns: 1fr;
		gap: 5px;
	}

	/* Featured Stories panel */
	.simple-grid .featured-stories-panel {
		grid-column: main-start / main-end;
		grid-row: head-start / foot-end;
		display: grid;
		grid-template-rows: 80px max-content auto 3%;
		grid-template-columns: 1fr;
		gap: 5px;
	}

	/* Header */
	.simple-grid header {
		margin: 0;
		height: var(--gridHeaderHeight);
	}

	/* Controls */
	.simple-grid .float-right-bottom {
		grid-column: 11 / side-end;
		grid-row: side-start / foot-end;
		align-self: end;
		justify-self: end;
		margin-bottom: 25px;
		align-items: end;
		padding: 0;
		height: 100%;
		width: 100%;
	}

	/* Timeline */
	.simple-grid .float-mid {
		grid-column: main-end / side-start;
		grid-row: extf-start;
	}

	/* Time */
	.simple-grid .float-mid-bottom {
		grid-template-columns: minmax(250px, 30%) 70%;
		grid-template-rows: 50% 50%;
		grid-column: main-end / side-start;
		grid-row: foot-start / foot-end;
		align-self: end;
		align-items: center;
		justify-items: start;
		height: auto;
		display: grid;
		gap: 0 30px;
		position: static;
		margin-bottom: 25px;
	}

	/* Compare */
	.simple-grid .compare {
		display: grid;
		grid-template-rows: 65px auto 40px;
		grid-template-columns: 50% 50%;
		grid-row: head-start / foot-end;
		grid-column: main-start / foot-end;
	}
}
