.checkboxes-block {
	width: 90%;
	height: 200px;
}

.cb-block-title{
	font-weight: bold;
	margin: 35px 0px 15px 0px;
}

.cb-container {
	display: block;
	position: relative;
	padding-left: 35px;
	margin-bottom: 12px;
	cursor: pointer;
	font-size: 16px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
  }

  .cb-container input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
  }

  .checkmark {
	position: absolute;
	top: 2px;
	left: 5px;
	height: 20px;
	width: 20px;
	background-color: #eee;
	border-radius: 3px;
  }

  .cb-container:hover input ~ .checkmark {
	background-color: #ccc;
  }

  .cb-container input:checked ~ .checkmark {
	background-color: #0D67FF;
  }

  .checkmark:after {
	content: "";
	position: absolute;
	display: none;
  }

  .cb-container input:checked ~ .checkmark:after {
	display: block;
  }

  .cb-container .checkmark:after {
	left: 7px;
	top: 3px;
	width: 3px;
	height: 9px;
	border: solid white;
	border-width: 0 3px 3px 0;
	-webkit-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	transform: rotate(45deg);
  }
