.buttons-block {
	display: grid;
	grid-auto-rows: 1fr;
	width: 90%;
	height: 300px;
	font-family: Metropolis, sans-serif;
}

.block-title {
	font-weight: 600;
	margin-top: 20px;
}

.custom-button {
	display: flex;
	gap: 20px;
	align-items: center;
	border: 1px solid black;
	background-color: #202021;
}

.custom-button:hover {
	background-color: #343434;
}

.buttons-block .focused {
	background-color: #343434;
	border-left: solid #0D67FF;
}

.button-icon {
	width: 56px;
	height: 56px;
}
