/* CSS unique to the following panel that will override the base Panel CSS */

#panel-container.following-panel .panel-simple {
	pointer-events: none;
}

.following-panel .panel-main,
.following-panel .panel-simple .expand-button {
	display: none;
}

.following-panel .panel-simple > h4.simple-title {
	margin: 0.1em 0 0 10px;
	filter: drop-shadow(0 0 0.1rem rgb(0 0 0 / 70%));
}
