@keyframes fade-in {
	0% { opacity: 0; }
	100% { opacity: 1; }
}

@keyframes fade-out {
	0% { opacity: 1; }
	100% { opacity: 0; }
}

@keyframes fade-in-left {
	0% {
		opacity: 0;
		transform: translateX(-100%);
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

@keyframes fade-out-left {
	0% {
		opacity: 1;
		transform: translateX(0);
	}
	100% {
		opacity: 0;
		transform: translateX(-100%);
	}
}

@keyframes fade-out-bottom {
	0% {
		transform: translateY(0);
		opacity: 1;
	}
	100% {
		transform: translateY(100%);
		opacity: 0;
	}
}
@keyframes fade-in-bottom {
	0% {
		transform: translateY(100%);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}

@keyframes pulse {
	0% {
		opacity: 0.75;
		transform: scale(1);
	}
	30% {
		opacity: 0.1;
		transform: scale(0.0);
	}
	70% {
		opacity: 0.1;
		transform: scale(0.0);
	}
	100% {
		opacity: 0.75;
		transform: scale(1);
	}
}

@keyframes spin {
	100% {
		transform: rotate(360deg);
	}
}

@keyframes blink {
	0% {
		outline: 1px transparent solid;
	}
	25% {
		outline: 1px var(--error) solid;
	}
	50% {
		outline: 4px var(--error) solid;
	}
	75% {
		outline: 1px var(--error) solid;
	}
	100% {
		outline: 1px transparent solid;
	}
}

/* Animation transitions */

:root {
	--ease-out-bezier: cubic-bezier(0.2, 0.2, 0, 1);
}
