/* Metropolis */
@font-face {
	font-family: 'Metropolis';
	font-weight: 100;
	src: url("./assets/default/fonts/Metropolis-Thin.woff");
}
@font-face {
	font-family: 'Metropolis';
	font-weight: 300;
	src: url("./assets/default/fonts/Metropolis-Light.woff");
}
@font-face {
	font-family: 'Metropolis';
	font-weight: 600;
	src: url("./assets/default/fonts/Metropolis-SemiBold.woff");
}

/* Inter */
@font-face {
	font-family: 'Inter';
	font-style: normal;
	font-weight: 300;
	font-display: swap;
	src:
		url("./assets/default/fonts/Inter-Light.woff2?v=3.15") format("woff2"),
		url("./assets/default/fonts/Inter-Light.woff?v=3.15") format("woff");
}
@font-face {
	font-family: 'Inter';
	font-style: normal;
	font-weight: 400;
	font-display: swap;
	src:
		url("./assets/default/fonts/Inter-Regular.woff2?v=3.15") format("woff2"),
		url("./assets/default/fonts/Inter-Regular.woff?v=3.15") format("woff");
}
@font-face {
	font-family: 'Inter';
	font-style: normal;
	font-weight: 600;
	font-display: swap;
	src:
		url("./assets/default/fonts/Inter-SemiBold.woff2?v=3.15") format("woff2"),
		url("./assets/default/fonts/Inter-SemiBold.woff?v=3.15") format("woff");
}
@font-face {
	font-family: 'Inter';
	font-style: normal;
	font-weight: 700;
	font-display: swap;
	src:
		url("./assets/default/fonts/Inter-Bold.woff2?v=3.15") format("woff2"),
		url("./assets/default/fonts/Inter-Bold.woff?v=3.15") format("woff");
}

/* DM Mono */
@font-face {
	font-family: 'DMMono';
	font-weight: 300;
	src: url("./assets/default/fonts/DMMono-Light.woff");
}
@font-face {
	font-family: 'DMMono';
	font-weight: 400;
	src: url("./assets/default/fonts/DMMono-Regular.woff");
}
@font-face {
	font-family: 'DMMono';
	font-weight: 500;
	src: url("./assets/default/fonts/DMMono-Medium.woff");
}
