.learn-modal .learn-modal-description {
	padding: 2px 56px 18px 56px;
	color: var(--grayblueMed);
	font-weight: 200;
	font-size: 20px;
	letter-spacing: 1px;
	text-align: center;
	border-bottom: 2px solid var(--grayblueDivider);
}
.learn-modal .learn-modal-body {
	padding: 0 28px 14px 28px;
	overflow-y: auto;
	max-height: calc(100vh - 206px);
}

.learn-modal .learn-modal-story-card {
	position: relative;
	padding: 28px 28px 28px 48px;
	border-bottom: 1px solid var(--grayblueDivider);
	cursor: pointer;
}
.learn-modal .learn-modal-story-card:last-child {
	border-bottom: none;
}
.learn-modal .learn-modal-story-card-hover {
	position: absolute;
	top: 10px;
	left: 50%;
	height: calc(100% - 20px);
	background: linear-gradient(to right, #1a1c1f77, var(--grayblueDivider), #1a1c1f77);
	z-index: -1;
	border-radius: 6px;
	pointer-events: none;
	opacity: 0;
	width: 80%;
	transform: translateX(-50%);
	transition: opacity 0.5s ease-out, width 0.2s ease-out;
}
.learn-modal .learn-modal-story-card-title {
	font-size: 27px;
	line-height: 27px;
	font-weight: 600;
	margin-bottom: 2px;
	letter-spacing: 1px;
	color: var(--grayblueLight);
}
.learn-modal .learn-modal-story-card-question {
	font-size: 18px;
	color: var(--grayblueText);
	margin-left: 36px;
	padding-top: 8px;
	letter-spacing: 1px;
}

@media only screen and (max-width: 641px) {
	.learn-modal .learn-modal-description {
		padding: 2px 20px 18px 20px;
		font-size: 18px;
	}
	.learn-modal .learn-modal-body {
		padding: 0 20px;
		max-height: calc(100vh - 281px);
	}
	.learn-modal .learn-modal-story-card {
		padding: 20px;
	}
	.learn-modal .learn-modal-story-card-title {
		font-size: 22px;
		line-height: 22px;
		margin-bottom: 8px;
	}
	.learn-modal .learn-modal-story-card-question {
		font-size: 16px;
		margin-left: 24px;
	}
}

@media only screen and (max-width: 390px) {
	.learn-modal .learn-modal-description {
		padding: 2px 20px 10px 20px;
		font-size: 16px;
	}
}

@media (pointer: fine) and (hover: hover) {
	.learn-modal .learn-modal-story-card:hover .learn-modal-story-card-hover {
		width: 100%;
		opacity: 1;
	}
}

