h1,
h2,
h3,
h4,
h5 {
	color: var(--grayblueWhite);
}

.hidden a {
	pointer-events: none;
}

button {
	font-size: inherit;
	line-height: 150%;
	font-family: "Metropolis", sans-serif;
	font-weight: 300;
	letter-spacing: 0.02em;
	color: var(--grayblueWhite);
}

.description {
	color: var(--grayblueMed);
	filter: brightness(1.2);
}

.carousel .content-block a {
	color: var(--grayblueLight);
}

@media (pointer: fine) and (hover: hover) {
	a:hover {
		color: var(--grayblueWhite);
		transition: color 0.3s ease-out;
	}
}

/*    DEEPDIVE    */
/*
 <div class="eyes-deepdive" data-storytitle="asteroids101" data-storyslide="1">Deep Dive into Asteroids 101</div>"
*/
.eyes-deepdive {
	display: inline-block;
	min-height: 37px;
	border-radius: 50px;
	text-align: center;
	padding: 8px 16px 8px 12px;
	margin-bottom: 8px;
	font-weight: 600;
	line-height: 21px;
	color: var(--grayDark);
	background: var(--grayMed);
	cursor: pointer;
	transition: background 0.2s ease-out;
}
@media (pointer: fine) and (hover: hover) {
	.eyes-deepdive:hover {
		background: var(--grayLight);
	}
}
.eyes-deepdive::before {
	content: "";
	display: inline-block;
	vertical-align: middle;
	width: 20px;
	height: 20px;
	margin-right: 6px;
	margin-top: -3px;
	color: var(--grayDark);
	background: url(./assets/svg/circuit_brain.svg);
	background-size: cover;
}
@media only screen and (max-width: 440px) {
	.eyes-deepdive {
		font-size: 13px;
	}
}

/*    END DEEPDIVE    */
