/* This is a bit all over the place and needs better structure */

.time-slider .slider-line {
	background: no-repeat center/100% url(./assets/default/svg/time_slider_lines.svg);
	transform: scaleX(1);
	visibility: visible;
	opacity: 1;
	transition: transform 1s 0.3s var(--ease-out-bezier);
}

.no-slider .slider-line,
.hidden .slider-line {
	transform: scaleX(0);
	visibility: hidden;
	opacity: 0;
	transition: transform 0.4s ease-in-out, opacity 0.4s 0.2s ease-in, visibility 0.4s 0.2s ease-in;
}


.time-slider .slider-icon {
	position: relative;
	width: 32px;
	height: 25px;
	justify-self: center;
	background: no-repeat center/100% url(./assets/svg/icon_clock.svg);
	visibility: visible;
	opacity: 1;
	pointer-events: all;
	cursor: grab;
	transition: transform 1s cubic-bezier(0.3, 1.24, 0.34, 0.98), opacity 0.75s 0.25s var(--ease-out-bezier), visibility 0.75s 0.25s var(--ease-out-bezier);
	transform: translateX(var(--x-icon-trans));
	z-index: 1;
}

.no-slider .slider-icon {
	visibility: hidden;
	opacity: 0;
	transition: opacity 0.3s 0.2s ease-in, visibility 0.3s 0.2s ease-in;
}

.no-slider .realtime {
	opacity: 0;
	pointer-events: none;
}

.time-slider .slider-icon::before {
	content: "";
	position: absolute;
	inset: -40% -40% -60% -40%;
}

.time-slider .slider-icon:active {
	cursor: grabbing;
}

.time-slider .slider-icon::after {
	content: var(--x-rate, "");
	color: var(--grayMed);
	letter-spacing: 0.02em;
	font-weight: 600;
	font-size: 0.8em;
	position: absolute;
	left: var(--customIconLabelMargin);
	transform: translate(-50%, -80%);
	white-space: nowrap;
}


/* This is a bit all over the place and needs better structure */
.time-container {
	position: relative;
	row-gap: 1rem;
	justify-self: center;
	align-self: center;
	width: calc(100% - 2rem);
	max-width: min(calc(50% - 1rem), 500px);
	padding-bottom: 1rem;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr 1fr;
	display: grid;
	grid-template-areas:
		"live date date"
		"slider slider slider";
	visibility: visible;
	opacity: 1;
	transition: all 0.5s var(--ease-out-bezier);
}

/* Black radial bg gradient */
.time-container::after {
	content: "";
	position: absolute;
	inset: -100% -40%;
	background-image: radial-gradient(rgb(0 0 0 / 50%) 0%, transparent 70%);
	z-index: -1;
}

.time-container.hidden {
	display: grid;
	margin-top: 76px;
	visibility: hidden;
	opacity: 0;
	transition-delay: 0.3s;
}

.time-container.offset-right {
	transform: translateX(calc(50% + 16px));
}

.time-container.no-slider {
	transition-delay: 0.3s;
	row-gap: 0;
}

.time-container .time-slider {
	grid-area: slider;
	display: grid;
	filter: drop-shadow(0 0 0.2rem rgb(0 0 0)) drop-shadow(0 0 1rem rgb(0 0 0)) drop-shadow(0 2px 1.5rem rgb(0 0 0));
}

.time-container .slider-line,
.time-container .slider-icon {
	grid-area: 1 / 1;
}

.time-container .realtime {
	position: relative;
	grid-area: 1 / 1;
	justify-self: center;
	align-self: center;
	width: 0.4rem;
	height: 0.4rem;
	border-radius: 0.2rem;
	background-color: var(--grayblueLight);
	padding: 0;
	z-index: 1;
	pointer-events: all;
	transition-property: filter, opacity, transform;
	transition-duration: 0.3s;
	transition-timing-function: var(--ease-out-bezier);
}

.time-container .realtime::before {
	content: "";
	position: absolute;
	inset: 0;
	border-radius: 0.3rem;
	border: 0.05rem var(--grayblueLight) solid;
	opacity: 0;
	transform: scale(0.5);
	transition-property: opacity, transform;
	transition-duration: 0.5s;
	transition-timing-function: var(--ease-out-bezier);
}

.time-container .realtime::after {
	content: "";
	position: absolute;
	inset: -0.5rem;
}

.time-container .realtime.hidden {
	display: unset;
	opacity: 0;
	pointer-events: none;
}

.touch .time-container .realtime {
	transform: scale(1.1);
	filter: brightness(1.2);
	cursor: pointer;
}

.touch .time-container .realtime::before {
	opacity: 1;
	transform: scale(2);
}

.time-container .clock-shortcut {
	grid-area: live;
	grid-template-rows: auto;
	transition: transform 0.5s var(--ease-out-bezier);
	align-self: center;
}

.time-container .clock-shortcut .live-container {
	font-size: 0.9rem;
	line-height: 1;
	border-radius: 20px;
	border: 1px solid var(--grayblueMed);
	justify-self: flex-start;
	align-self: end;
	padding: 0.3em 1.2em 0.3em 0.6em;
	margin-left: 4px;
	background-repeat: no-repeat;
	background-position: center;
	background-image: radial-gradient(var(--liveAlpha) 60%, transparent 70%);
	background-size: 0% 0%;
	filter: drop-shadow(0 0 0.3rem rgb(0 0 0/70%)) drop-shadow(0 0 1rem rgb(0 0 0));
}

.time-container .clock-shortcut .live-container.active {
	border: none;
	margin: 1px 1px 1px 5px;
	filter: drop-shadow(0 0 1rem rgb(0 0 0));
}

.time-container .clock-shortcut .live-container.active .text {
	line-height: 0;
}

@media (pointer: fine) and (hover: hover) {
	.time-container .realtime:hover {
		transform: scale(1.1);
		filter: brightness(1.2);
		cursor: pointer;
	}

	.time-container .realtime:hover::before {
		opacity: 1;
		transform: scale(2);
	}

	.time-container .clock-shortcut .live-container:hover {
		background-color: transparent;
		background-size: 150% 150%;
		transition: background-size 0.25s var(--ease-out-bezier);
	}

	.time-slider .slider-icon::after {
		transform: translate(-50%, 110%);
	}
}

.time-container .clickable .icon-live {
	background-position: 0 -76px;
}


.time-container .clock {
	grid-area: date;
	justify-self: stretch;
	align-self: center;
}

.time-container .clock .datetime-container {
	margin: 0;
}

.time-container .clock .datetime-container .display-container {
	opacity: 1;
	grid-template-columns: 1fr 1fr;
	align-items: center;
	filter: drop-shadow(0 0 0.15rem rgb(0 0 0)) drop-shadow(0 0 1rem rgb(0 0 0));
}

.time-container .clock .date {
	font-family: DMMono, monospace;
	justify-self: center;
	text-transform: uppercase;
	transition: transform 0.5s var(--ease-out-bezier);
	color: var(--grayblueLight);
}

.time-container .clock .time,
.time-container .clock .meridiem {
	font-family: DMMono, monospace;
	font-size: 0.8rem;
	grid-column-start: 2;
	grid-row-start: 1;
	justify-self: flex-end;
	align-self: flex-end;
	transition: transform 0.5s var(--ease-out-bezier);
	filter: brightness(1.2);
	color: var(--grayblueMed);
}

.time-container .clock .meridiem {
	margin-right: 8px;
}

.time-container .clock .time {
	margin-right: 32px;
}

.time-container .datetime-container > form,
.time-container input.hour-input {
	text-align: center;
	width: unset;
}

.time-container input.hour-input {
	backdrop-filter: blur(10px);
	background-color: rgb(0 0 0/50%);
}


/* Limited vertical space and portrait */
.time-container.portrait {
	max-width: unset;
}

/* Offset vertically when not in home view */
.watch-view .time-container.portrait,
.mission-view .time-container.portrait,
.following-view .time-container.portrait,
.asteroid-view .time-container.portrait {
	transform: translateY(calc(100% + 12px));
}


.time-container.v-squeezy {
	row-gap: 0.7rem;
}
.time-container.h-squeezy .clock .date,
.time-container.v-squeezy .clock .date,
.time-container.portrait .clock .date {
	font-size: 14px;
}
.time-container.h-squeezy .clock .time,
.time-container.h-squeezy .clock .meridiem,
.time-container.h-squeezy .live-container,
.time-container.v-squeezy .clock .time,
.time-container.v-squeezy .clock .meridiem,
.time-container.v-squeezy .live-container,
.time-container.portrait .clock .time,
.time-container.portrait .clock .meridiem,
.time-container.portrait .live-container {
	font-size: 12px;
}


@media only screen and (min-width: 1025px) {
	.time-container {
		margin-bottom: 5vh;
	}

	.time-container.offset-right {
		transform: translateX(var(--offset-right-lg));
	}
}

/* Exclude panorama. Big landscape tablets, laptops/desktops, and above */
@media only screen and (min-width: 1025px) and (min-height: 600px) {
	.time-container {
		margin-bottom: 0;
	}

	.time-container .clock .date {
		font-size: 18px;
	}

	.time-container .clock .meridiem {
		font-size: 14px;
		margin-right: 8px;
	}

	.time-container .clock .time {
		font-size: 14px;
		margin-right: 32px;
	}

	.time-container .slider-icon {
		width: 40px;
		height: 26px;
	}
}
