.load-icon {
	position: fixed;
	width: 80px;
	height: 80px;
	top: 50%;
	left: 50%;
	z-index: 1000;
	display: flex;
	flex-wrap: wrap;
	transform: translate(-50%, -50%);
	transition: transform 0.5s var(--ease-out-bezier);
}
.load-icon.hidden {
	display: none;
}

/* Offsets */
.load-icon.offset-right {
	transform: translate(calc(var(--offset-right-sm, 0) - 50%), -50%);
	transition-duration: 0.8s;
}

.load-icon.offset-up {
	transform: translate(-50%, calc(-50% - var(--offset-up, 0)));
	transition-duration: 0.8s;
}

.load-icon .text {
	position: absolute;
	bottom: -1em;
	left: 50%;
	transform: translateX(-50%);
	color: var(--grayLight);
	font-size: 1em;
	text-transform: uppercase;
}
.load-icon .top,
.load-icon .right,
.load-icon .bottom,
.load-icon .left,
.load-icon .front,
.load-icon .bg {
	position: absolute;
	top: 0;
	left: 0;
	background: transparent url(./assets/default/svg/cubemap.svg) no-repeat 0 -400px;
	width: 80px;
	height: 80px;
	display: block;
}
.load-icon .bg {
	opacity: 0.15;
}
.load-icon .top,
.load-icon .right,
.load-icon .bottom,
.load-icon .left,
.load-icon .front {
	animation: pulse 2s infinite;
}
.load-icon .front {
	background-position-y: -320px;
	transform-origin: 47px 48px;
}
.load-icon .left {
	background-position-y: 0;
	transform-origin: 17px 44px;
	animation-delay: 0.25s;
}
.load-icon .top {
	background-position-y: -80px;
	transform-origin: 41px 18px;
	animation-delay: 0.5s;
}
.load-icon .right {
	background-position-y: -160px;
	transform-origin: 67px 39.5px;
	animation-delay: 0.75s;
}
.load-icon .bottom {
	background-position-y: -240px;
	transform-origin: 40px 67px;
	animation-delay: 1s;
}

/* Over 961 wide */
@media only screen and (min-width: 961px) {
	/* Offsets */
	.load-icon.offset-right {
		transform: translate(calc(var(--offset-right-lg, 0) - 50%), -50%);
	}
}
