.settings {
	display: none;
	justify-content: flex-end;
	transition: transform 1.0s ease-in-out;
	margin-left: auto;
}
.settings.active {
	display: flex;
	align-items: flex-end;
	align-self: end;

	/* Animation */
	animation-name: fade-in-bottom;
	animation-duration: 0.95s;
	animation-timing-function: ease-in-out;
	animation-iteration-count: 1;
	animation-fill-mode: both;
}
.settings.vertical {
	flex-direction: column;
}
.settings.horizontal {
	flex-direction: row;
}
.settings.hidden {
	display: flex;

	/* Animation */
	animation-name: fade-out-bottom;
	animation-duration: 1.0s;
	animation-timing-function: ease-in-out;
	animation-iteration-count: 1;
	animation-fill-mode: both;
}

/* Buttons */
.settings button {
	padding: 0;
	outline: 0;
}
.settings button.zoom-in {
	margin-bottom: 0;
}
.settings button span {
	opacity: 0.4;
}
.settings button.hidden {
	display: none;
}

/* Container */
.settings .container {
	display: block;
	position: relative;
	flex-basis: auto;
	transition: all 1s;
	overflow: hidden;
}
.settings .container.active {
	flex-grow: 1;
	flex-shrink: 1;
}
.settings .container.hidden {
	flex-grow: 0;
}
.settings.vertical .container {
	width: 100%;
}
.settings.horizontal .container {
	height: 100%;
}

/* Overlay */
.settings .overlay-bg {
	opacity: 0.6;
	width: 100vw;
	height: var(--vh);
	margin-right: -20px;
	z-index: 100;
	position: fixed;
	background-color: var(--black);
}

/* Content */
.settings .container .content {
	position: absolute;
	overflow: hidden;
	height: 100%;
	display: grid;
}
.settings.vertical .container .content {
	grid-auto-flow: row;
	row-gap: 2px;
	align-items: end;
}
.settings.horizontal .container .content {
	grid-auto-flow: column;
	column-gap: 2px;
	align-items: start;
}

/* Toggle */
.settings.vertical .toggle {
	margin-top: 2px;
}

/* Zoom button */
.settings .zoom {
	margin: 0;
}
.settings .horizontal-line {
	background: var(--grayDark);
	box-sizing: border-box;
	margin: auto;
}
.settings.vertical .zoom {
	display: grid;
	grid-template-rows: 30px 2px 30px;
	grid-template-columns: 30px;
}
.settings.vertical .horizontal-line {
	height: 2px;
	width: 22px;
}
.settings.horizontal .zoom {
	display: flex;
}
.settings.horizontal .horizontal-line {
	height: 22px;
	width: 2px;
	display: inline-block;
}

/* Lighting options */
.settings .lighting-option-container {
	right: 34px;
	bottom: 34px;
	padding: 8px 10px;
	position: fixed;
	background: var(--bgGradient);
	border-radius: 3px;
	z-index: 101;
}
.settings .lighting-option {
	display: grid;
	grid-template-columns: auto auto;
	gap: 8px;
	align-items: center;
	padding: 4px 0;
}
.settings .lighting-option .title {
	opacity: 0.4;
	white-space: nowrap;
	pointer-events: none;
}

.settings .lighting-option-container .lighting-option.selected-lighting span {
	opacity: 1;
}

.settings .lighting-option .lighting-icon {
	min-width: 16px;
	pointer-events: none;
}
@media (pointer: fine) and (hover: hover) {
	.settings .lighting-option:hover .lighting-icon {
		opacity: 1;
	}
	.settings .lighting-option:hover .title {
		opacity: 1;
	}
}

/* 480x320 phones and above */
@media only screen and (min-width: 320px) {
	.settings {
		margin-left: auto;
	}
	.settings.active {
		animation-name: fade-in-bottom;
		animation-duration: 0.95s;
		animation-timing-function: ease-in-out;
		animation-iteration-count: 1;
		animation-fill-mode: both;
	}
	.settings.hidden {
		display: flex;
		animation-name: fade-out-bottom;
		animation-duration: 1.0s;
		animation-timing-function: ease-in-out;
		animation-iteration-count: 1;
		animation-fill-mode: both;
	}
	.settings .container .content {
		row-gap: 2px;
	}
	.settings .overlay-bg {
		bottom: -105px;
	}
}

/* Landscape. 480x320 phones and above */
@media only screen and (min-width: 320px) and (orientation: landscape) {
	.settings .overlay-bg {
		bottom: -10px;
		right: -56px;
	}
}

/* Exclude panorama. Big landscape tablets, laptops/desktops, and above */
@media only screen and (min-width: 1025px) and (min-height: 600px) {
	.settings .overlay-bg {
		bottom: -25px;
		right: unset;
	}
}
