.kiosk-container {
	position: absolute;
	inset: 0;
	z-index: calc(var(--overlay-z-index) * 2);
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: repeat(5, 1fr);
}

.session-end-container {
	position: absolute;
	inset: 0;
	z-index: calc(var(--overlay-z-index) * 2);
	display: grid;
	justify-items: center;
	align-items: center;
	background: rgba(0  0  0 / 75%);
}

.session-end-buttons-container {
	z-index: calc(var(--overlay-z-index) * 2);
	pointer-events: all;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-rows: repeat(2, 1fr);
	column-gap: 3em;
}

.session-ended-msg {
	align-self: center;
	grid-column: 2 / span 2;
	font-size: 1.75em;
	font-weight: 600;
	margin: 1em;
	text-align: center;
}

.kiosk-btn {
	display: grid;
	align-items: center;
	background-color: var(--grayblueButton);
	width: 14em;
	height: 4.5em;
	border-radius: 0.5em;
	font-size: 1.75em;
	font-weight: 600;
	text-align: center;
}

.session-end-btn {
	grid-column: span 2;
	grid-row: 2;
}

.force-restart {
	grid-column: 2 / span 2;
}

.autoplay-container {
	display: grid;
	justify-self: center;
	align-self: center;
	grid-column-start: 2;
	grid-row-start: 3;
	pointer-events: all;
}

.touch-to-start-btn {
	grid-column-start: 2;
	grid-row-start: 3;
}

.session-timer-container {
	display: flex;
	justify-self: end;
	align-self: center;
	align-items: center;
	grid-template-columns: 1fr 1fr;
	grid-column-start: 3;
	grid-row-start: 1;
	margin-right: 1em;
	color: rgb(255 255 188);
	font-size: 1.5em;
}

.session-countdown-timer {
	opacity: 0.6;
}

.loading-text {
	display: grid;
	justify-self: center;
	align-self: center;
	align-items: center;
	grid-column-start: 2;
	grid-row-start: 4;
	z-index: calc(var(--overlay-z-index) * 4);
	padding: 1em;
	background: rgba(0 0 0 / 35%);
	border-radius: 5px;
	color: white;
	text-align: center;
	font: "Helvetica Neue", Helvetica, Arial, sans-serif;
	font-size: 2.5em;
	font-weight: 600;
}
