/* Generic */
* {
	box-sizing: border-box;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
}
input,
textarea {
	user-select: text;
	font: inherit;
}
html,
body {
	height: 100%;
	overflow: hidden;
	position: relative;
	text-size-adjust: none;
}
html {
	height: var(--vh);
}
body {
	font-size: 16px;
	line-height: 150%;
	letter-spacing: 0;
	font-family: Metropolis, sans-serif;
	font-weight: 300;
	color: var(--white);
}
body .semi {
	letter-spacing: 0.02em;
}

/* Visibility */
.hidden {
	display: none;
}
.invisible {
	visibility: hidden;
}

/* Interactions */
.editable {
	pointer-events: all;
	cursor: text;
}
.clickable {
	pointer-events: all;
	cursor: pointer;
}
.clickable.underline {
	position: relative;
	color: var(--white);
	opacity: 0.75;
	text-decoration: underline;
	cursor: pointer;
	transition: opacity 0.25s ease-in-out;
}
@media (pointer: fine) and (hover: hover) {
	.clickable.underline:hover {
		opacity: 1;
	}
}

/* Font properties */
.wide-text {
	letter-spacing: 0.6em;
}
.semi {
	font-weight: 600;
}
.small {
	font-size: 14px;
	line-height: 150%;
	letter-spacing: 0;
	font-family: Metropolis, sans-serif;
}
.x-small {
	font-size: 12px;
	line-height: 180%;
	letter-spacing: 0.03em;
	font-family: Metropolis, sans-serif;
}
.tiny {
	font-size: 11px;
	line-height: 180%;
	letter-spacing: 0.03em;
	font-family: Metropolis, sans-serif;
}
.uppercase {
	text-transform: uppercase;
}
.capitalize {
	text-transform: capitalize;
}

/* Lines */
.vertical-line {
	border: 1px solid #ffffff;
	box-sizing: border-box;
	width: 2px;
	height: 20px;
}

/* Headers */
.display,
h1,
h2,
h3,
h4,
h5 {
	color: var(--white);
	font-family: Metropolis, sans-serif;
	letter-spacing: -0.02em;
	margin: 0;
}
.display.semi,
h1.semi,
h2.semi,
h3.semi,
h4.semi,
h5.semi {
	letter-spacing: -0.02em;
}
.display {
	font-size: 48px;
	line-height: 120%;
	font-weight: 100;
}
h1 {
	font-size: 41px;
	line-height: 120%;
	font-weight: 100;
}
h2 {
	font-size: 36px;
	line-height: 120%;
	font-weight: 100;
}
h3 {
	font-size: 29px;
	line-height: 95%;
	font-weight: 300;
}
h3.semi {
	line-height: 100%;
}
h4 {
	font-size: 22px;
	line-height: 120%;
	font-weight: 300;
}
h5 {
	font-size: 18px;
	line-height: 180%;
	font-weight: 300;
}
h5.semi {
	line-height: 150%;
}

/* Buttons */
button {
	background-color: transparent;
	border: 0;
}

/* Support line break in text */
.line-break-text {
	white-space: pre-line;
}

/* Alert outline */
.alert {
	outline: none;
	outline-offset: 4px;
	animation-name: blink;
	animation-duration: 0.5s;
	animation-timing-function: ease-in-out;
	animation-iteration-count: 3;
	animation-direction: alternate;
}

/* Checkbox style using custom color */
.checkbox {
	background-color: var(--grayDark);
	border-radius: 3px;
	width: 15px;
	height: 15px;
}

/* Tooltip */
.tippy-box[data-theme~="default"] {
	background-color: var(--grayDark);
	border-color: var(--grayDark);
	color: var(--grayLight);
	opacity: 0.9;
}
.tippy-box[data-theme~="default"][data-placement^="top"] > .tippy-arrow::before {
	border-top-color: var(--grayDark);
}
.tippy-box[data-theme~="default"][data-placement^="bottom"] > .tippy-arrow::before {
	border-bottom-color: var(--grayDark);
}
.tippy-box[data-theme~="default"][data-placement^="left"] > .tippy-arrow::before {
	border-left-color: var(--grayDark);
}
.tippy-box[data-theme~="default"][data-placement^="right"] > .tippy-arrow::before {
	border-right-color: var(--grayDark);
}
