/* Asteroids-specific label CSS */

.pioneer-label-div {
	filter: brightness(1);
	transition: filter 3s 1s ease-in-out, opacity 0.1s var(--ease-out-bezier);
}


/* ------------------------------- Asteroid Watch ------------------------------- */

.pioneer-label-div.asteroid-watch-label {
	padding-left: 2rem;
}


.pioneer-label-div.asteroid-watch-label.hidden {
	pointer-events: auto;
	display: block;

	/* Increased transparency label collisions as we always want to see all 5 */
	/* stylelint-disable-next-line declaration-no-important */
	opacity: var(--secondaryFadeIn) !important;
}

.pioneer-label-div.asteroid-watch-label span.asteroid-watch-text {
	opacity: 1;
	filter: brightness(0.8);
	color: var(--grayblueLight);
}

.pioneer-label-div.asteroid-watch-label.selected span.asteroid-watch-text {
	opacity: 1;
	filter: brightness(1);
	transform: translateX(-0.25rem);
	transition: filter 0.4s var(--ease-out-bezier), transform 0.4s var(--ease-out-bezier);
}

.pioneer-label-div.asteroid-watch-label span.asteroid-watch-icon,
.pioneer-label-div.asteroid-watch-label.hidden span.asteroid-watch-icon {
	opacity: 1;
	filter: brightness(0.8);
	transform: translate(-50%, -50%) scale(1.2);
}

.pioneer-label-div.asteroid-watch-label.selected span.asteroid-watch-icon {
	opacity: 1;
	filter: brightness(1);
	transform: translate(-50%, -50%) scale(1.5);
}

/* Background label dimming */
.pioneer-label-div.watch-dim {
	filter: brightness(0.25);
}

.pioneer-label-div.asteroid-watch-label .icon {
	display: unset;
}
.pioneer-label-div.asteroid-watch-label .icon.hidden {
	display: none;
}


/* ------------------------------- Overview labels ------------------------------- */


/* Comet label needs a little more left padding due to offset icon */
.pioneer-label-div.comet {
	padding-left: 1.7rem;
}

.pioneer-label-div.spacecraft .icon {
	width: 0.6rem;
}

/* Spacecraft label can go slightly closer */
.pioneer-label-div.spacecraft {
	padding-left: 0.75rem;
}

/* Planet, sun and moon text can go a bit closer */
.pioneer-label-div.planet,
.pioneer-label-div.sun,
.pioneer-label-div.moon {
	padding-left: 0.8rem;
}

.pioneer-label-div.planet .icon,
.pioneer-label-div.moon .icon {
	background-image: none;
	width: 0.5rem;
	height: 0.5rem;
	border-radius: 0.25rem;
	opacity: 1;
	filter: brightness(0.85);
	transition-property: transform, filter;
}

/* Make comet and asteroid icons slightly smaller */
.pioneer-label-div .icon.asteroid {
	width: 1.1rem;
	height: 1.1rem;
}
.pioneer-label-div .icon.comet {
	width: 1.4rem;
	height: 1.4rem;
}

.pioneer-label-div .text {
	text-shadow: 0 0 0.25rem black, 0 0 0.3rem black;
	display: block;
	transition: filter 0.4s, transform 0.4s var(--ease-out-bezier);
}

.pioneer-label-div .text.hidden {
	display: none;
}

/* Adjust text default opacity, weight and size range */
.pioneer-label-div.comet .text,
.pioneer-label-div.asteroid .text,
.pioneer-label-div.spacecraft .text,
.pioneer-label-div.dwarf-planet .text,
.pioneer-label-div.moon .text,
.pioneer-label-div.sun .text,
.pioneer-label-div.planet .text {
	opacity: 1;
	filter: brightness(0.8);
	font-weight: 400;
	font-size: clamp(var(--fontSizeSmall), 1.4vw, var(--fontSize));
	letter-spacing: 0.02em;
}

/* Adjust caps text spacing and size */
.pioneer-label-div.moon .text,
.pioneer-label-div.sun .text,
.pioneer-label-div.planet .text {
	letter-spacing: 0.1em;
	font-weight: 600;
	font-size: clamp(var(--fontSizeTiny), 1.2vw, var(--fontSizeXSmall));
}

/* Adjust text colors */
.pioneer-label-div.sun .text {
	color: var(--sunAsteroids);
}
.pioneer-label-div.moon .text {
	text-transform: uppercase;
	color: var(--moon);
}

.pioneer-label-div.spacecraft .text {
	color: var(--spacecraftAsteroids);
	filter: brightness(0.7);
}

.pioneer-label-div.comet .text,
.pioneer-label-div.asteroid .text,
.pioneer-label-div.dwarf-planet .text {
	color: var(--grayblueLight);
}

.pioneer-label-div.sun,
.pioneer-label-div.planet {
	letter-spacing: unset;
}

/* Planets */
.pioneer-label-div.mercury {
	color: var(--mercury);
}
.pioneer-label-div.venus {
	color: var(--venus);
}
.pioneer-label-div.earth {
	color: var(--earth);
}
.pioneer-label-div.mars {
	color: var(--mars);
}
.pioneer-label-div.jupiter {
	color: var(--jupiter);
}
.pioneer-label-div.saturn {
	color: var(--saturn);
}
.pioneer-label-div.uranus {
	color: var(--uranus);
}
.pioneer-label-div.neptune {
	color: var(--neptune);
}


/* Make sure all icons have opacity 1 but reduced brightness */
.pioneer-label-div.active .icon {
	opacity: 1;
	filter: brightness(0.7);
	transition: filter 0.25s, transform 0.25s ease-in-out;
}

/* Hover overrides */
@media (pointer: fine) and (hover: hover) {
	.pioneer-label-div:hover .icon,
	.pioneer-label-div:hover .text {
		filter: brightness(1);
	}

	.pioneer-label-div:hover .text {
		transform: translateX(-0.25rem);
	}

	.pioneer-label-div.active:hover .icon.comet {
		transform: translate(-25%, -75%) scale(1.2);
	}

	.pioneer-label-div.asteroid-watch-label:hover span.asteroid-watch-text {
		opacity: 1;
		filter: brightness(1);
		transform: translateX(-0.25rem);
		transition: filter 0.4s var(--ease-out-bezier), transform 0.4s var(--ease-out-bezier);
	}

	.pioneer-label-div.asteroid-watch-label:hover span.asteroid-watch-icon {
		opacity: 1;
		filter: brightness(1);
		transform: translate(-50%, -50%) scale(1.5);
	}

	.pioneer-label-div.watch-dim:hover {
		filter: brightness(1);
		transition: filter 0.8s var(--ease-out-bezier);
	}
}

