/* Carousel content */
.panel-content .mission-carousel {
	padding: 1em 0 4.5em;
	height: 200px;
}

#panel-container.mission-panel a {
	color: var(--grayblueLight);
	text-decoration: underline;
	transition: color 0.2s ease-in;
}

/* Bullets */
.mission-carousel .bullet-container {
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
	margin: 1em auto 3.8em;
}


/* Slides */
.mission-carousel .swiper-slide {
	padding: 0.5em 0;
	mask-image: linear-gradient(to bottom, transparent 5px, white 10px, white 80%, transparent 100%);
}

.mission-carousel .swiper-slide h4 {
	font-size: 18px;
	letter-spacing: 0;
	color: var(--grayblueWhite);
	padding: 0 0 0.75em;
}

.mission-carousel .swiper-slide p {
	margin: 0;
	color: var(--grayblueMed);
	font-size: 14px;
	line-height: 1.6;
}

.mission-carousel .swiper-slide p.long {
	padding: 0 1.5em 0.1em 0;
}

.mission-carousel .swiper-slide .value {
	position: absolute;
	right: 1px;
	transform: translateY(85%);
	border: 1px solid var(--grayblueMed);
	border-radius: 24px;
	color: var(--grayblueText);
	font-size: 10px;
	line-height: 1;
	padding: 0.6em 2em 0.6em 1.1em;
	transition: filter 0.2s ease-in;
}

.mission-carousel .swiper-slide .value::after {
	content: "›";
	position: absolute;
	transform: scale(1.7);
	margin-left: 0.6em;
	line-height: 0.8;
}

.mission-carousel .swiper-slide .value:active {
	filter: brightness(1.5);
	transition: filter 0.4s ease-out;
}


/* Anything over 961, we can incresed some font and icon sizes  slightly */
@media only screen and (min-width: 961px) {
	/* Carousel content */
	.panel-content .mission-carousel {
		height: 275px;
	}

	/* Bullets */
	.mission-carousel .bullet-container {
		margin: 1em auto 4.2em;
	}

	/* Text */
	.mission-carousel .swiper-slide h4 {
		font-size: 20px;
	}

	.mission-carousel .swiper-slide p {
		font-size: 18px;
		line-height: 1.8;
	}

	.mission-carousel .swiper-slide .value {
		font-size: 12px;
	}
}

/* Less than 641px and portrait */
@media only screen and (max-width: 641px) and (orientation: portrait) {
	/* Main padding */
	#panel-container.mission-panel .panel-main {
		padding: 12px 20px;
	}

	/* Carousel content */
	.panel-content .mission-carousel {
		height: 175px;
		padding: 0.5em 0 3em;
	}

	/* Slides */
	.mission-carousel .swiper-slide {
		padding: 0.5em 0;
		mask-image: linear-gradient(to bottom, transparent 2px, white 6px, white 90%, transparent 100%);
	}

	/* Bullets */
	.mission-carousel .bullet-container {
		margin: 1em auto 2em;
	}

	/* Adjust text */
	.mission-carousel .swiper-slide h4 {
		padding: 0 0 0.4em;
	}
}

/* Height-related queries */
@media only screen and (max-height: 641px) {
	/* Main padding */
	#panel-container.mission-panel .panel-main {
		padding: 16px 20px;
	}

	#panel-container.mission-panel .header-title > h4.title-text {
		font-size: 22px;
	}

	/* Carousel content */
	.panel-content .mission-carousel {
		height: min(220px, 50vh);
		padding: 1.8em 0 2.5em;
	}

	/* Prev and next buttons */
	.mission-carousel .swiper-button-prev,
	.mission-carousel .swiper-button-next {
		bottom: 0.7em;
	}

	/* Bullets */
	.mission-carousel .bullet-container {
		margin: 0 auto 1em;
	}

	/* Text */
	.mission-carousel .swiper-slide h4 {
		font-size: 16px;
	}
	.mission-carousel .swiper-slide p {
		line-height: 1.4;
	}
}


@media (pointer: fine) and (hover: hover) {
	.mission-carousel .swiper-slide .value:hover {
		filter: brightness(1.5);
		transition: filter 0.4s ease-out;
	}

	#panel-container.mission-panel a:hover {
		color: var(--grayblueWhite);
		transition: color 0.3s ease-out;
	}
}
