.header > nav.breadcrumb {
	z-index: 2;
	order: 1;
}

.breadcrumb .home .text {
	position: relative;
	display: inline-block;
	white-space: nowrap;
	letter-spacing: 0.04em;
	font-size: 0.9rem;
	font-weight: 600;
	top: 0.08em;
	filter: drop-shadow(0 0 0.2rem rgb(0 0 0));
}

.breadcrumb .home .icon {
	filter: drop-shadow(0 0 0.2rem rgb(0 0 0));
}

@media only screen and (min-width: 961px) {
	.breadcrumb .home .text {
		font-size: 1rem;
	}
}
