:root {
	--overlay-z-index: 9999;
}
/* Scrollbar CSS override */
.os-theme-dark > .os-scrollbar,
.os-scrollbar-vertical,
.os-host-resize-disabled.os-host-scrollbar-horizontal-hidden > .os-scrollbar-vertical {
	right: 6px;
	top: 40px;
	bottom: 40px;
}
.os-theme-dark > .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle {
	background: var(--grayDark);
	width: 4px;
}
.os-theme-dark > .os-scrollbar:active > .os-scrollbar-track > .os-scrollbar-handle {
	background: var(--gray);
	width: 6px;
}
@media (pointer: fine) and (hover: hover) {
	.os-theme-dark > .os-scrollbar:hover > .os-scrollbar-track > .os-scrollbar-handle {
		background: var(--gray);
		width: 6px;
	}
}
.os-theme-dark > .os-scrollbar-vertical > .os-scrollbar-track > .os-scrollbar-handle {
	min-height: 20px;
}

/* Overlay */
.overlay-container .overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	z-index: var(--overlay-z-index);
	height: var(--vh);
	background: black;
	opacity: 0.9;
}

.overlay-container .overlay.active {
	animation-duration: 0.5s;
	animation-timing-function: ease-in;
	animation-iteration-count: 1;
	animation-fill-mode: both;
	animation-name: fade-in;
	display: flex;
	pointer-events: all;
}
.overlay-container .overlay.hidden {
	pointer-events: none;
}
.overlay-container.initialized .overlay.hidden {
	animation-duration: 0.5s;
	animation-timing-function: ease-out;
	animation-iteration-count: 1;
	animation-fill-mode: both;
	animation-name: fade-out;
	display: flex;
}
.overlay-container.initialized .overlay.hidden .clickable {
	display: none;
}

.overlay-container .overlay .close-button {
	position: absolute;
	top: 0;
	right: 0;
	z-index: 10;
	transform: scale(1.5);
	transform-origin: top right;
	opacity: 0.4;
	margin: 20px 20px 0 0;
	padding: 0;
}

@media (pointer: fine) and (hover: hover) {
	.overlay-container .overlay .close-button:hover {
		opacity: 1;
	}
}

/* Landscape. 480x320 phones and above */
@media only screen and (min-width: 320px) and (orientation: landscape) {
	.overlay-container .overlay {
		left: -20px;
		width: calc(100vw + 20px);
	}
}
