.image-block {
	display: inline-flex;
	flex-direction: column;
	align-items: center;
	max-height: 70vh;
}

.image-block.fullscreen {
	display: flex;
	justify-content: center;
	width: 100%;
	height: var(--vh);
	padding: 5%;
	max-width: 100%;
	max-height: 100%;
	margin: 0;
}

.image-block .title {
	margin: 2px 0 12px 0;
	color: #aaaaaa;
	text-align: center;
}

.image-block img {
	width: 100%;
	height: auto;
	max-width: 100%;
	max-height: 100%;
	object-fit: contain;
	overflow: hidden;
}

/* 480x320 phones and above */
@media only screen and (min-width: 320px) {
	.image-block {
		height: 50vh;
		margin-bottom: 8px;
		padding-bottom: 25px;
		font-size: 12px;
	}
}

@media only screen and (max-height: 600px) and (orientation: landscape) {
	.carousel-container .carousel .slide.overlay .container .content-wrapper .content {
		flex-direction: unset;
	}
	.carousel-container .carousel .slide.overlay .image-block {
		height: 100%;
		width: 40%;
		display: flex;
		justify-content: center;
		margin-bottom: auto;
	}
	.image-block + .description-block {
		height: 100%;
		width: 60%;
		display: flex;
		justify-content: center;
		flex-flow: column;
	}
}

/* Exclude panorama. Big landscape tablets, laptops/desktops, and above */
@media only screen and (min-width: 1025px) and (min-height: 600px) {
	.image-block {
		height: auto;
		padding-bottom: 0;
		font-size: 14px;
	}
}
