.filters-modal .filters-modal-description {
	padding: 2px 36px 18px 36px;
	font-weight: 200;
	font-size: 16px;
	letter-spacing: 1px;
	color: var(--grayblueMed);
	text-align: center;
}
.filters-modal .filters-modal-body {
	padding: 28px;
	border-top: 1px solid var(--grayblueDivider);
	border-bottom: 1px solid var(--grayblueDivider);
}
.filters-section {
	margin-bottom: 30px;
}
.filters-section:last-child {
	margin-bottom: 0;
}
.filters-section-item {
	display: flex;
	justify-content: space-between;
}
.filters-section-item > div:last-child {
	margin: 12px 0 auto 0;
}
.filters-modal .filters-section-item .eyes-checkbox {
	margin: auto 48px auto 0;
}
.filters-modal .filters-section-item .eyes-toggle {
	margin: auto 38px auto 0;
}
.filters-section-h {
	font-size: 14px;
	color: var(--grayblueLight);
	font-weight: 600;
	line-height: 21px;
	padding: 0 2px;
	margin-bottom: 12px;
	letter-spacing: 1px;
}
.filters-section-h2 {
	font-size: 18px;
	font-weight: 400;
	line-height: 26px;
	letter-spacing: 1px;
	color: var(--grayblueLight);
}
.filters-section-h2 > div:first-child {
	display: flex;
	height: 28px;
	line-height: 28px;
}
.filters-section-h2 > div:last-child {
	font-size: 14px;
	line-height: 21px;
	color: #95959d;
}
.filters-help {
	padding: 4px;
	margin-left: 16px;
	color: var(--grayblueMed);
	transition: color 0.2s ease-in;
	cursor: pointer;
}
.filters-help * {
	pointer-events: none;
}
.filters-section-content {
	margin-left: 46px;
}

.filters-modal .filters-modal-footer {
	display: flex;
	justify-content: space-between;
	padding: 16px 18px 16px 18px;
	white-space: nowrap;
}
.filters-modal-reset {
	cursor: pointer;
	text-decoration: underline;
	padding: 6px 12px 10px 12px;
	font-size: 17px;
	font-weight: 200;
	letter-spacing: 0.5px;
	height: 37px;
	color: var(--grayblueMed);
	transition: color 0.2s ease-in;
}
.filters-modal-count {
	height: 37px;
	padding: 8px 12px 8px 12px;
	color: var(--grayblueText);
	font-size: 16px;
	font-weight: 600;
	letter-spacing: 0.5px;
}

@media only screen and (max-width: 641px) {
	.filters-modal .filters-section-item .eyes-checkbox,
	.filters-modal .filters-section-item .eyes-toggle {
		margin: auto 0;
	}
	.filters-section-content {
		margin-left: 20px;
	}
}
@media only screen and (max-width: 350px) {
	.filters-modal-description {
		display: none;
	}
	.filters-modal-reset {
		font-size: 12px;
		color: var(--white);
	}
	.filters-modal-count {
		font-size: 12px;
	}
	.filters-modal .filters-modal-body {
		padding: 18px;
	}
}
@media only screen and (max-height: 590px) {
	.filters-modal-description {
		display: none;
	}
}
@media (pointer: fine) and (hover: hover) {
	.filters-help:hover {
		color: var(--grayblueWhite);
	}
	.filters-modal-reset:hover {
		color: var(--grayblueWhite);
	}
}
