/* Home button CSS */
.home-button {
	white-space: nowrap;
	grid-column-start: 1;
	grid-row-start: 2;
	align-self: flex-start;
	justify-self: flex-start;
	color: var(--grayblueText);
	font-size: 1rem;
	letter-spacing: 0;
	opacity: 1;
	visibility: visible;
	transform: translateX(0);
	transition: filter 0.2s, opacity 0.6s, visibility 0.6s, transform 0.6s;
	transition-timing-function: var(--ease-out-bezier);
	filter: drop-shadow(0 0 0.1rem rgb(0 0 0 / 30%));
}

.home-button.hidden {
	opacity: 0;
	visibility: hidden;
	transform: translateX(-60%);
	transition: opacity 0.3s, visibility 0.3s, transform 0.4s;
	display: unset;
}

/* Arrow */
.home-button::before {
	content: "‹";
	position: relative;
	top: 0.1rem;
	font-size: 1.8rem;
	font-weight: 600;
	line-height: 0;
	margin: 0 0.75rem 0 0.2rem;
}

/* Increase touch/click area */
.home-button::after {
	content: "";
	position: absolute;
	inset: 0% -15% -15% -6%;
}

@media only screen and (max-width: 641px) {
	.home-button {
		margin-left: 20px;
	}
}

@media only screen and (max-height: 600px) {
	.home-button {
		margin-top: 1vh;
	}
}

@media (pointer: fine) and (hover: hover) {
	.home-button:hover {
		filter: brightness(1.5);
		transition: filter 0.4s ease-out;
	}
}
