/* Container */
.layer-panel {
	background: var(--bgGradient);
	border-radius: 3px;
	pointer-events: all;
}

/* Header */
.layer-panel .header {
	padding-top: 20px;
	display: grid;
	grid-template-columns: auto auto;
	margin: 0 10px;
	cursor: pointer;
}

/* List */
.layer-panel .layers {
	padding: 0;
	margin: 10px 0;
}
.layer-panel .layers li {
	height: 100%;
	list-style: none;
	display: flex;
	margin-bottom: 5px;
	cursor: pointer;
	opacity: 0.7;
	transition: opacity 0.2s ease-out;
	gap: 0;
}
.layer-panel .layers li:last-child {
	margin-bottom: 0;
}
.layer-panel .layers li.active,
.layer-panel .layers li:hover {
	opacity: 1;
}
.layer-panel .layers li > * {
	pointer-events: none;
}
.layer-panel .layers li > span.checkbox {
	margin: 3px 0;
}
.layer-panel .layers li > *:last-child {
	margin-left: 10px;
	line-height: 24px;
}
.layer-panel .layers li.disabled {
	opacity: 0.3;
	pointer-events: none;
}

@media (pointer: fine) and (hover: hover) {
	.layer-panel .item:hover .checkbox-hover {
		background-color: var(--gray);
	}
}

/* 480x320 phones and above */
@media only screen and (min-width: 320px) {
	.layer-panel {
		position: fixed;
		bottom: 0;
		right: 0;
		left: 0;
		width: 100%;
		z-index: 10;
		background: var(--mobileBgGradient);
	}
	.layer-panel .container {
		display: grid;
		grid-template-columns: calc(50% - 5px) calc(50% - 5px);
		gap: 0 10px;
		padding: 20px 35px 40px;
	}

	/* Header */
	.layer-panel .header {
		justify-content: center;
	}
	.layer-panel .header .close {
		transform: rotate(90deg);
	}
}

/* Landscape. 480x320 phones and above */
@media only screen and (min-width: 320px) and (orientation: landscape), /* Portrait. Portrait tablets/iPad and above */
	only screen and (min-width: 641px) and (orientation: portrait) {
	.layer-panel .container {
		display: grid;
		grid-template-columns: calc(33% - 10px) calc(33% - 10px) calc(33% - 10px);
		grid-template-rows: max-content auto;
		gap: 0 15px;
		padding: 20px 35px 10px;
	}
}

/* Exclude panorama. Big landscape tablets, laptops/desktops, and above */
@media only screen and (min-width: 1025px) and (min-height: 600px) {
	.layer-panel {
		position: absolute;
		right: 0;
		bottom: unset;
		left: unset;
		background: var(--bgGradient);
		border-radius: 3px;
		width: auto;
	}
	.layer-panel .container {
		display: block;
		grid-template-columns: unset;
		padding: 20px 35px 40px;
	}
	.layer-panel .block {
		border-right: 0;
		border-bottom: 1px solid #404040;
	}
	.layer-panel .block:last-child {
		border: 0;
	}

	/* Header */
	.layer-panel .header {
		justify-content: end;
	}
	.layer-panel .header .close {
		transform: none;
		opacity: 0.6;
	}
	@media (pointer: fine) and (hover: hover) {
		.layer-panel .header .close:hover {
			opacity: 1;
		}
	}
}
