/* Watch Card CSS */
.watch-card {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	max-width: 335px;
	width: 100%;
	margin: 0 auto;
}

/* Text Group */
.watch-card .text-group {
	width: 60%;
}

.watch-card .text-group > h3 {
	color: var(--grayblueLight);
}

.text-group .date-block {
	margin-top: 0.7em;
}

.text-group .distance-block {
	margin-top: 0.4em;
}

.date-block > .date-text {
	display: flex;
	align-items: baseline;
}

.date-text > .date-day,
.date-text > .date-time {
	text-transform: uppercase;
	white-space: nowrap;
}

.date-text > .date-time {
	margin-left: 1em;
}

.diameter-text {
	text-align: center;
}

.date-text > .date-day,
.date-text > .date-time,
.distance-text > .distance-value,
.distance-text > .distance-unit,
.diameter-text > .diameter-value,
.diameter-text > .diameter-unit {
	color: var(--grayblueLight);
}

.diameter-text .diameter-estimated {
	display: block;
	font-size: 12px;
	text-transform: uppercase;
	letter-spacing: 0.05em;
	color: var(--grayblueMed);
}


.distance-block > .distance-text {
	display: flex;
	align-items: center;
}

.date-block > span.date-label,
.distance-block > span.distance-label {
	color: var(--grayblueText);
}

/* Rock Group */
.watch-card .rock-group {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}


.distance-block h5.distance-unit,
.rock-group span.diameter-unit {
	margin-left: 0.3em;
	text-decoration: underline;
}


/* Less than 961px */
@media only screen and (max-width: 961px) {
	/* Text Group */
	.watch-card .text-group > h3 {
		font-size: 22px;
	}

	.watch-card .text-group > span,
	.text-group span.date-time {
		font-size: 12px;
	}

	.date-block .date-text > h5,
	.distance-block .distance-text > h5 {
		font-size: 14px;
	}


	/* Rock Group */
	.watch-card .rock-group {
		width: 25%;
		min-width: 65px;
	}

	.rock-group .icon.rock,
	.rock-group .icon.ruler {
		transform: scale(0.7);
	}

	.rock-group .diameter-text > span {
		font-size: 12px;
	}

	.diameter-text span.diameter-estimated {
		font-size: 10px;
		letter-spacing: 0.02em;
	}
}
