/* Layout */
body {
	overscroll-behavior: none;
	margin: 0;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100%;
	color: black;
}

#pioneer {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}

.ui {
	position: absolute;
	left: 0;
	top: 0;
	width: 100vw;
	height: 100%;
	min-width: 320px;
	pointer-events: none;
}

.ui .clickable {
	pointer-events: all;
	cursor: pointer;
}

.ui .hidden .clickable {
	pointer-events: none;
	cursor: unset;
}

/* Components */
.time-controller .rate-container {
	display: grid;
	grid-template-rows: auto;
	justify-content: start;
	align-items: center;
}

.clock-shortcut {
	grid-row: 1;
	grid-column: 1;
	display: grid;
}

.extended-controls {
	display: grid;
	grid-template-columns: auto auto;
	align-items: center;
	width: 100%;
}

.grid-layout header .breadcrumb {
	height: 45px;
	grid-column: 1;
	grid-row: 1;
}

.grid-layout header .top-right-nav {
	grid-column: 2;
	grid-row: 1;
	justify-self: end;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	gap: 1rem;
	font-family: Inter, Metropolis, sans-serif;
}

body.story-view header .top-right-nav {
	margin-right: 6rem;
}

/* 480x320 phones and above */
@media only screen and (min-width: 320px) {
	.clock {
		grid-row: 1;
		grid-column: 2;
		align-self: end;
		justify-self: end;
	}

	.time-controller {
		justify-self: stretch;
	}
	.time-controller .rate-container {
		grid-template-columns: auto 85px 2px 85px auto;
		grid-template-rows: 30px;
	}
	.time-controller .rate-container .play-pause {
		display: none;
	}

	.clock-shortcut {
		grid-template-rows: auto auto;
	}

	.extended-controls {
		border-top: 1px solid var(--grayDark);
		grid-column: span 2;
		padding-top: 10px;
		margin-bottom: 10px;
	}
}

/* Landscape. 480x320 phones and above */
@media only screen and (min-width: 320px) and (orientation: landscape) {
	.clock-shortcut {
		grid-template-rows: auto;
		grid-template-columns: auto auto;
	}
}

/* Exclude panorama. Big landscape tablets, laptops/desktops, and above */
@media only screen and (min-width: 1025px) and (min-height: 600px) {
	.clock {
		grid-row: 2;
		grid-column: 1;
		align-self: auto;
		justify-self: auto;
	}

	.time-controller {
		justify-self: auto;
	}
	.time-controller .rate-container {
		grid-template-columns: auto auto 90px 2px 90px auto;
		grid-template-rows: 30px;
	}
	.time-controller .rate-container .play-pause {
		display: block;
	}

	.clock-shortcut {
		grid-column: span 2;
		grid-template-rows: auto;
		grid-template-columns: auto auto;
	}

	.extended-controls {
		border-top: none;
		grid-row: 2;
		grid-column: 2;
		padding-top: 0;
		margin-bottom: 0;
	}
}
