.replay-button-block {
	display: inline-block;
	padding-top: 20px;
}

.replay-button-block button {
	display: grid;
	grid-template-columns: 30px auto;
	gap: 0 2px;
	align-items: center;
	padding: 1px 7px 1px 2px;
	background: var(--grayDarkAlpha);
	color: var(--white);
	border-radius: 5px;
}

.replay-button-block .text {
	text-transform: uppercase;
}

.replay-button-block .icon,
.replay-button-block .text {
	opacity: 0.4;
}

.replay-button-block:hover button {
	background: var(--grayAlpha);
}

.replay-button-block:hover .icon,
.replay-button-block:hover .text {
	opacity: 1;
	color: var(--white);
}
