.navigation.menu-div.bottom {
	grid-column-start: 1;
	grid-row-start: 2;
	grid-auto-flow: column;
	display: grid;
	column-gap: 15px;
	align-content: center;
	padding: 14px 0 24px;
	border-top: 1px solid var(--grayDark);
	background: linear-gradient(180deg, var(--grayblueBackground) 0.7%, #000000 100%);
	transform: translateY(0);
	opacity: 1;
	transition: 0.6s var(--ease-out-bezier);
	transition-property: transform, opacity;
}

.navigation.menu-div.bottom.hidden {
	transform: translateY(100%);
	opacity: 0;
	transition-duration: 0.3s;
}

.navigation.menu-div.bottom button {
	cursor: pointer;
}

.navigation.menu-div.bottom button h2 {
	cursor: pointer;
	font-size: 12px;
	line-height: 1.5;
	font-weight: 600;
	letter-spacing: 0.02em;
	color: var(--grayblueLight);
}

.navigation.menu-div.bottom > div {
	padding: 0;
	display: block;
	text-align: center;
	position: relative;
	opacity: 1;
}
.navigation.menu-div.bottom img {
	display: block;
	opacity: 1;
	margin: 0 auto;
	transform: translate(0, 0);
	width: 22px;
	height: 22px;
	filter: brightness(1.2);
}
.navigation.menu-div.top {
	z-index: 1;
	display: none;
	position: relative;
	margin-left: auto;
	order: 2;
}

.navigation.menu-div.top::before {
	content: "";
	position: absolute;
	inset: -100% -40%;
	background-image: radial-gradient(rgb(0 0 0 / 70%) 0%, transparent 70%);
}

.navigation.menu-div.top button {
	cursor: pointer;
}

.navigation.menu-div.top > div {
	position: relative;
	opacity: 0.8;
	transition-duration: 0.2s;
	transition-timing-function: var(--ease-out-bezier);
	transition-property: opacity, transform;
}

.navigation > div .navigation-badge {
	opacity: 0;
}

.navigation.menu-div.top > div.badged,
.navigation.menu-div.top > div.active {
	transform: translateX(8px);
	transition-duration: 0.5s;
}

.navigation > div.badged .navigation-badge {
	position: absolute;
	top: 0;
	left: calc(50% + 7px);
	width: 6px;
	height: 6px;
	background: #f93f2e;
	border-radius: 50%;
	opacity: 1;
	transition: opacity 0.3s 0.2s ease-out;
}


@media only screen and (hover: hover) {
	.navigation.menu-div.top > div:hover {
		opacity: 1;
		transform: translateX(8px);
		transition-duration: 0.5s;
	}
}


@media only screen and (min-width: 641px) {
	.navigation.menu-div.top {
		display: grid;
		grid-auto-flow: column;
		gap: 0;
		position: relative;
		transform: translateY(0);
		opacity: 1;
		transition: 0.6s var(--ease-out-bezier);
		transition-property: transform, opacity;
	}

	.navigation.menu-div.top.hidden {
		transform: translateY(-100%);
		opacity: 0;
		transition-duration: 0.3s;
	}

	.navigation.menu-div.top > div {
		white-space: nowrap;
		padding: 0 0.7vw;
		display: flex;
		position: relative;
	}

	.navigation.menu-div.top > div.badged img,
	.navigation.menu-div.top > div.active img,
	.navigation.menu-div.top > div:hover img {
		opacity: 1;
		filter: brightness(1.3);
		transform: translate(0, 0) scale(0.8);
	}

	.navigation > div.badged .navigation-badge {
		left: 24px;
		top: 2px;
	}

	.navigation.menu-div.top > div img,
	.navigation.menu-div.top > div button {
		transition: all 300ms;
	}
	.navigation.menu-div.top > div button h2 {
		font-size: 14px;
		font-weight: 600;
		letter-spacing: 0.02em;
	}
	.navigation.menu-div.top > div img {
		opacity: 0;
		margin-right: 3px;
		transform: translate(-10px, 0) scale(0.88);
	}
	.navigation.menu-div.bottom {
		display: none;
	}
}


@media only screen and (min-width: 961px) {
	/* .navigation.menu-div.top {
		padding-right: 80px;
	} */
	.navigation.menu-div.top > div {
		white-space: nowrap;
		padding: 0 20px;
		display: flex;
	}
	.navigation.menu-div.top > div.badged img,
	.navigation.menu-div.top > div.active img,
	.navigation.menu-div.top > div:hover img {
		opacity: 1;
		transform: translate(0, 0) scale(0.9);
	}

	.navigation > div.badged .navigation-badge {
		left: 39px;
		top: 2px;
	}

	.navigation.menu-div.top > div button h2 {
		font-size: 16px;
	}
	.navigation.menu-div.top > div img {
		margin-right: 10px;
		transform: translate(-15px, 0) scale(0.9);
	}
}

@media only screen and (min-width: 961px) and (hover: hover) {

	.navigation.menu-div.top > div:hover img {
		transform: translate(0, 0) scale(0.9);
	}
}

/* Landscape. Landscape iPad, big tablet, lo-res laptops/desktops, and above */
@media only screen and (max-width: 961px) and (orientation: landscape) and (hover: none) {
	.navigation.menu-div.top {
		gap: 3vw;
		padding-right: 5vw;
		display: flex;
		align-items: center;
	}
	.navigation.menu-div.top > div {
		padding: 0;
		display: block;
		text-align: center;
		white-space: nowrap;
	}
	.navigation.menu-div.top > div button h2 {
		font-size: 12px;
		line-height: 1.5;
	}
	.navigation.menu-div.top > div img {
		display: block;
		width: 22px;
		height: 22px;
		opacity: 1;
		margin: 0 auto;
		transform: translate(0, 0);
	}
	.navigation.menu-div.bottom > div img {
		display: block;
		opacity: 1;
		margin: 0 auto;
		transform: translate(0, 0);
	}
	.navigation.menu-div.bottom {
		display: none;
	}
}


/* Landscape mobile */
@media only screen and (max-width: 641px) and (orientation: landscape) {
	.navigation.menu-div.bottom {
		width: calc(100% + 40px);
		left: -20px;
		position: relative;
	}
}

@media only screen and (max-width: 641px) and (orientation: landscape) and (hover: none) {
	.navigation.menu-div.top > div {
		padding: 0 0.5vw;
	}
}
