.search-open .featured {
	position: absolute;
	top: 100%;
	width: 100%;
	height: 300px;
	max-height: calc(var(--vh) - 86px);
	left: 0;
	border-radius: 0 0 12px 12px;
	border-top: 1px solid var(--grayDivider);
	transform-origin: top;
	opacity: 1;
	transform: scaleY(1);
	transition: transform 0.3s 0.2s var(--ease-out-bezier), opacity 0.3s 0.2s var(--ease-out-bezier);
}

.search-close .featured.hidden {
	display: unset;
	transform: scaleY(0);
	opacity: 0;
	height: 0;
	transition: transform 0.2s var(--ease-out-bezier), opacity 0.2s var(--ease-out-bezier);
}

.search .featured ul {
	font-size: 14px;
	list-style: none;
	margin: 0;
	padding: 24px 0 68px;
	opacity: 1;
	transition: opacity 0.5s 0.2s ease-out;
}

.search .featured.hidden ul {
	opacity: 0;
}

.search .featured li {
	margin: 9px 40px 9px 16px;
	padding: 8px 16px;
	border-radius: 6px;
	background-image: radial-gradient(at 10%, #303030 0%, transparent 110%);
	background-repeat: no-repeat;
	background-position: center;
	background-size: 0% 0%;
	color: var(--grayMed);
}

@media (hover: hover) {
	.search .featured li:hover {
		background-size: 100% 100%;
		transition: background-size 0.15s ease-out;
		color: var(--white);
	}

	.search .mag-open:hover {
		filter: brightness(1.2);
		transform: scale(1.05);
		transition: filter 0.2s, transform 0.2s;
	}
}

/* Search menu */
.top-right-nav .search {
	order: 1;
}
.top-right-nav .mega-menu {
	order: 3;
}

/* Search */
.search {
	display: block;
	pointer-events: none;
	justify-self: end;
	opacity: 1;
	transition: opacity 0.6s ease-out, visibility 0.6s ease-out;
}
.search .bar {
	position: relative;
	display: grid;
	background: var(--grayDarkAlpha80);
	padding: 7.5px;
	border-radius: 0.2em;
}
.search .cover {
	pointer-events: all;
	height: var(--vh);
	width: 100vw;
	background: var(--black);
	position: fixed;
	left: 0;
	top: 0;
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.4s ease-out, visibility 0.4s ease-out;
}

/* Search input */
.search .input-focused .cover {
	opacity: 0.6;
	visibility: visible;
	transition: opacity 0.6s ease-in, visibility 0.6s ease-in;
}
.search input[type="text"] {
	grid-area: input;
	background: transparent;
	padding: 0 10px;
	width: inherit;
	border: none;
	outline: none;
	color: var(--white);
}
.search .mag-open {
	grid-area: mag;
	display: block;
	margin: 7.5px;
	filter: brightness(0.8);
	transition: filter 0.1s, transform 0.1s;
}
.search .search-open {
	display: grid;
	pointer-events: all;
}
.search span.search-info {
	position: absolute;
	left: 50%;
	bottom: -24px;
	transform: translateX(-50%);
	color: var(--grayAlpha);
	font-weight: 600;
	text-transform: uppercase;
	z-index: 1;
	opacity: 1;
	transition: opacity 0.3s 0.3s ease-out;
}
.search-close span.search-info {
	opacity: 0;
	transition: opacity 0.2s ease-out;
}

/* Results */
.search .bar .results {
	position: absolute;
	top: 100%;
	width: 100%;
	left: 0;
	border-radius: 0 0 0.2em 0.2em;
	backdrop-filter: blur(0.5rem);
}

/* Only when results have children */
.search .bar .results:not(:empty) {
	border-top: 0.1em solid var(--grayDark);
}

.search .bar:has(.results:not(:empty)) {
	border-radius: 0.2em 0.2em 0 0;
}

.search .bar .results .result-div:first-child {
	margin-top: 0.6em;
}
.search .bar .results .result-div:last-child {
	margin-bottom: 0.4em;
}
.search .bar .results .block-container {
	margin-bottom: 20px;
}
.search .suggestion {
	padding: 20px 0;
}
.search .suggestion .title {
	display: block;
	padding: 2px 20px;
}
.search .gradient {
	background: linear-gradient(180deg, var(--grayDarkAlpha80) 0%, rgb(37 37 39 / 64%) 60.64%, rgb(37 37 39 / 0%) 116.42%);
}
.search .result-div {
	transition: all 0.3s;
	padding: 0.4em 1.3em;
}
.search .result-div button {
	padding: 0;
	text-align: left;
}

/* Search selection */
.search .result-div.active {
	background-color: var(--whiteAlpha15);
	cursor: pointer;
}
.search .result-div.active .clickable {
	text-decoration: underline;
}

.search .results .entries {
	display: block;
	outline: none;
	text-align: left;
	width: 100%;
	padding: 0;
}


/* Result card */
.search .card-title {
	padding: 15px 20px;
	border-bottom: 1px solid var(--grayDark);
	display: grid;
	grid-template-columns: 1fr auto;
	align-items: baseline;
	margin-bottom: 10px;
}
.search .bar .results .card-title.result-div:first-child {
	margin-top: 0;
}
.search .card-title img {
	width: 50px;
}

.search .results .unmatch {
	opacity: 0.6;
}

.search .detail .entry {
	opacity: 1.0;
	padding: 0;
}

.search .title {
	padding: 0 20px;
}

.search-close-icon {
	grid-area: close;
}

/* 480x320 phones and above */
@media only screen and (min-width: 320px) {
	.search .bar {
		margin: auto;
		width: 90%;
		grid-template-columns: 1fr auto;
		grid-template-areas: "input search";
	}

	.search .search-close-icon {
		display: none;
	}
}

@media (pointer: coarse) {
	.search .result-div.active {
		background-color: transparent;
		cursor: normal;
	}
}
