.breadcrumb {
	/* Variables */
	--inactiveOpacity: 0.7;
	--activeOpacity: 1.0;

	/* Properties */
	display: grid;
	align-items: center;
	justify-content: left;
}

.breadcrumb nav {
	display: inline-block;
	vertical-align: middle;
}

/* Use Nasa icon for home */
.breadcrumb .container.home {
	display: grid;
	grid-template-columns: auto 1fr;
	align-items: center;
	column-gap: 0.5em;
}

.breadcrumb .text {
	font-size: inherit;
	font-weight: 600;
	letter-spacing: 0.64px;
	color: var(--grayMed);
	display: contents;
}

.breadcrumb .link {
	display: inline-block;
}

.breadcrumb .text::first-letter {
	text-transform: uppercase;
}

.breadcrumb .home .text {
	display: none;
	vertical-align: middle;
}
.breadcrumb.tiny .text,
.breadcrumb.x-small .text,
.breadcrumb.small .text,
.breadcrumb.large .text {
	font-size: inherit;
}

/* Active or not */
.breadcrumb .container {
	opacity: var(--inactiveOpacity);
}
.breadcrumb .container.active,
.breadcrumb .container:last-child {
	opacity: var(--activeOpacity);
}
.breadcrumb .container.hidden {
	display: none;
}

/* Separator */
.breadcrumb .container .separator {
	transform: scale(0.66);
	display: inline-block;
	vertical-align: middle;
}

@media (pointer: fine) and (hover: hover) {
	.breadcrumb .container.home {
		opacity: var(--inactiveOpacity);
	}
	.breadcrumb .container:hover {
		opacity: var(--activeOpacity);
	}
}

/* 480x320 phones and above */
@media only screen and (min-width: 320px) {
	.breadcrumb {
		grid-auto-flow: row;
	}
	.breadcrumb .container {
		display: none;
	}

	@media (pointer: coarse) {
		.breadcrumb:hover .container:not(.home) {
			padding-left: 45px;
			display: grid;
			grid-template-columns: max-content auto;
		}

		.breadcrumb:hover .container.home {
			padding-left: 0;
		}
	}
}

/* Exclude panorama. Big landscape tablets, laptops/desktops, and above */
@media only screen and (min-width: 1025px) and (min-height: 600px) {
	.breadcrumb {
		grid-auto-flow: column;
	}
	.breadcrumb .container {
		padding: 0;
		display: grid;
		grid-template-columns: max-content max-content;
		align-items: center;
	}
	@media (pointer: coarse) {
		.breadcrumb:hover .container:not(.home) {
			padding: 0;
			display: grid;
		}
	}
}
