
.countdown {
	margin: 24px auto;
	padding: 20px 12px 14px 12px;
	position: relative;
	z-index: 0;
	max-width: 335px;
	width: 100%;
}

/* Masked border technique -> https://stackoverflow.com/questions/51496204/border-gradient-with-border-radius/51496341 */
.countdown::before {
	content: "";
	position: absolute;
	z-index: -1;
	inset: 0;
	padding: 2px;
	border-radius: 12px;
	background: radial-gradient(ellipse at top, transparent 34%, var(--live) 34.01%);
	-webkit-mask: linear-gradient(#ffffff 0 0) content-box, linear-gradient(#ffffff 0 0);
	mask: linear-gradient(#ffffff 0 0) content-box, linear-gradient(#ffffff 0 0);
	-webkit-mask-composite: destination-out;
	mask-composite: exclude;
}

.countdown .countdown-title {
	position: absolute;
	left: 50%;
	top: 0;
	transform: translate(-50%, -40%);
}

.countdown .digits-container {
	display: flex;
	justify-content: flex-end;
}

.digits-container .t-text {
	position: absolute;
	left: 5%;
	top: 28%;
}

.digits-container .digit-block {
	text-align: center;
	min-width: 22%;
}

.digits-container .digit-block > h3.digit {
	margin: 0 -0.1em -2px 0;
	letter-spacing: 0.1em;
}

.countdown .countdown-title,
.digits-container .digit,
.digits-container .digit-unit {
	color: var(--live);
}

/* Less than 961px */
@media only screen and (max-width: 961px) {
	.countdown .countdown-title {
		font-size: 12px;
	}

	.digits-container .t-text {
		font-size: 14px;
		top: 32%;
	}

	.digits-container .digit-block > h3.digit {
		font-size: 26px;
		margin: 0 -0.05em -4px 0;
	}

	.digits-container span.digit-unit {
		font-size: 10px;
		font-weight: 300;
	}
}
