.definition-overlay {
	position: absolute;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.7);
	display: flex;
	justify-content: center;
	align-items: center;
	opacity: 1;
	visibility: visible;
	z-index: 2001;
	pointer-events: all;
	transition: opacity 0.5s var(--ease-out-bezier), visibility 0.5s var(--ease-out-bezier);
}

.definition-overlay.hidden {
	display: flex;
	pointer-events: none;
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.2s ease-in, visibility 0.2s ease-in;
}

.definition-container {
	width: calc(100% - 32px);
	max-width: 500px;
	max-height: calc(100vh - 32px);
	background-color: var(--grayBackground);
	border-radius: 12px;
	display: flex;
	flex-direction: column;
	transition: transform 0.5s var(--ease-out-bezier);
}

.hidden .definition-container {
	display: flex;
	transform: translateY(50%);
	transition: transform 0.2s ease-in;
}

/* Header */
.definition-container .definition-header {
	height: 60px;
	border-bottom: 1px solid var(--grayDark);
	display: flex;
}

.definition-header h4.definition-title {
	flex: 1 0 auto;
	line-height: 60px;
	font-weight: 600;
	letter-spacing: 0;
}

.definition-header div.definition-info,
.definition-header div.definition-close {
	flex-basis: 64px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.definition-header div.definition-close {
	pointer-events: all;
	user-select: none;
}

div.definition-info span {
	transform: scale(0.9);
}

.definition-close span {
	transform: scale(1.6);
	opacity: 0.5;
	transition: opacity 0.25s ease-in-out;
}

/* Content */
.definition-container .definition-content {
	padding: 0 30px 4px 21px;
	margin: 24px 18px 16px 6px;
	scroll-behavior: smooth;
	mask-image:
		linear-gradient(
			to bottom,
			transparent 0,
			white 8px,
			white calc(100% - 16px),
			transparent 100%
		);
}

/* scrollbar */
#definition-overlay .os-scrollbar-vertical {
	right: 0;
	top: 5px;
	bottom: 5px;
	width: 12px;
}

.definition-content p:first-child {
	margin-top: 0;
}

.definition-content p,
.definition-content ul {
	/* font-weight: 300; */
	color: var(--grayMed);
}

.definition-content span {
	position: relative;
	color: var(--white);
	opacity: 0.75;
	text-decoration: underline;
	cursor: pointer;
	transition: opacity 0.25s ease-in-out;
}

.definition-content span::after {
	content: "";
	position: absolute;
	inset: -4px -8px;
}

.definition-content .oumuamua-animation::before {
	content: '';
	position: absolute;
	inset: 0;
	opacity: 0.25;
	background-color: radial-gradient(black 10%, transparent 100%);
	background-repeat: no-repeat;
	background-position: 66%;
	background-image: url(./assets/svg/oumuamua.svg);
	background-size: auto 100%;
	transform-origin: 66% 51%;
	animation: spin 20s linear infinite;
	z-index: -1;
}


/* Footer */
/* .definition-container .definition-footer {
	flex-basis: 70px;
} */

.definition-footer .definition-divider {
	position: relative;
	height: 1px;
	width: calc(100% - 42px);
	left: 21px;
	background-color: var(--grayDark);
}

.definition-divider span {
	position: absolute;
	left: 50%;
	transform: translate(-50%, -50%);
	line-height: unset;
	background-color: var(--grayBackground);
	padding: 0 12px;
	color: var(--gray);
	font-weight: 100;
}

.definition-footer .definition-related {
	display: flex;
	flex-wrap: wrap;
	gap: 8px;
	padding: 16px 21px 21px 21px;
}

.definition-related .definition-related-item {
	color: var(--white);
	opacity: 0.7;
	padding: 8px 16px;
	background-color: var(--grayDark);
	background-repeat: no-repeat;
	background-position: center;
	background-image: radial-gradient(#303030 10%, transparent 100%);
	background-size: 0% 0%;
	border-radius: 6px;
	line-height: unset;
	user-select: none;
}

@media (pointer: fine) and (hover: hover) {
	.definition-close span:hover {
		opacity: 0.8;
	}
	.definition-content span:hover {
		opacity: 1;
	}
	.definition-related .definition-related-item:hover {
		opacity: 1;
		background-size: 100% 100%;
		transition: background-size 0.25s var(--ease-out-bezier), opacity 0.3s var(--ease-out-bezier);
	}
}
