/* CSS unique to the watch panel that will override the base carousel panel CSS */
.watch-panel .panel-header {
	position: relative;
}

.watch-panel .panel-header::before,
.watch-panel .panel-header::after {
	content: '';
	position: absolute;
	height: 1px;
	width: 42%;
	left: -12px;
	bottom: -1.2em;
	background-color: var(--grayblueDivider);
}

.watch-panel .panel-header::after {
	left: unset;
	right: -12px;
}

/* Adjusting divider on widths below 961 */
@media only screen and (max-width: 961px) {

	.watch-panel .panel-header::before {
		width: 38%;
		left: 0;
	}

	.watch-panel .panel-header::after {
		width: 38%;
		right: 0;
	}
}

/* Saving vertical height below 641px */
@media only screen and (max-height: 641px) {

	.watch-panel .panel-header::after {
		content: none;
	}

	.watch-panel .panel-header::before {
		width: 100%;
		left: 0;
		bottom: -0.7em;
	}
}

/* Pagination fraction, ie. 1 OF 5 */
.watch-carousel > .watch-carousel-fraction {
	display: inline-table;
	top: 0.6em;
	text-transform: uppercase;
	color: var(--grayblueDivider);
	font-size: 14px;
	filter: brightness(1.5);
}


@media only screen and (max-width: 961px) {
	/* Smaller fraction font */
	.watch-carousel > .watch-carousel-fraction {
		font-size: 12px;
	}
}


/* When the height is less than 900px, we need to save a little on vertical space */
@media only screen and (max-height: 900px) and (max-width: 641px) {
	/* Reduce main swiper padding */
	.watch-panel .panel-content .swiper {
		padding: 1.8em 0 0 0;
	}

	/* Reduce caption padding */
	.watch-panel .panel-header .header-caption {
		line-height: 1.2;
	}

	/* Reduce countdown vertical margins */
	.watch-carousel .countdown {
		margin: 10px auto;
		padding: 13px 12px 7px 12px;
	}

	/* Adjust bullet margins */
	.watch-panel .panel-content .bullet-container {
		margin: 1em auto 1em auto;
	}

	/* Reposition nav arrows up */
	.watch-panel .panel-content .swiper-button-prev,
	.watch-panel .panel-content .swiper-button-next {
		bottom: 0.8em;
	}

	/* Smaller fraction font */
	.watch-carousel > .watch-carousel-fraction {
		font-size: 11px;
		top: 0;
	}
}

@media only screen and (max-height: 750px) and (min-height: 642px) {
	.watch-panel {
		margin-top: 50px;
	}
}

/* When the height is less than 641px, we need to save A LOT on vertical space */
/* Ways to save vertical space:
- hide the countdown
- hide the fractions
*/
@media only screen and (max-height: 641px) {
	/* Reduce main swiper padding */
	.watch-panel .panel-content .swiper {
		padding: 1em 0 0.5em 0;
	}

	/* Adjust bullet margins */
	.watch-panel .panel-content .bullet-container {
		margin: 1.2em auto 0.5em auto;
	}

	/* Reposition nav arrows up */
	.watch-panel .panel-content .swiper-button-prev,
	.watch-panel .panel-content .swiper-button-next {
		bottom: 0.8em;
	}

	/* hide clock */
	.watch-carousel .countdown {
		display: none;
	}

	/* hide fractions - divider lines changed in watch_panel.css */
	.watch-carousel > .watch-carousel-fraction {
		display: none;
	}
}

