/* Viewport offsets */

/* Offset vars */
:root {
	--offset-right-sm: 150px;
	--offset-right-lg: 200px;
	--offset-up: 22vh;
}

/* Pioneer pseudo element fading gradients */
#pioneer::after {
	content: '';
	position: absolute;
	inset: 0;
	opacity: 0;
	background-image: linear-gradient(to right, black var(--offset-right-sm), transparent calc(var(--offset-right-sm) + 20vw));
	transition: opacity 2s var(--ease-out-bezier);
	pointer-events: none;
}

#pioneer.offset-right::after {
	opacity: 1;
	transition: opacity 0.8s var(--ease-out-bezier);
}

#pioneer.offset-up::after {
	opacity: 1;
	transition: opacity 0.8s var(--ease-out-bezier);
}

/* Main Viewport offsets */
/* stylelint-disable declaration-no-important */
#main-viewport {
	transition-property: left, bottom;
	transition-duration: 0.5s;
	transition-timing-function: var(--ease-out-bezier);
	bottom: 0;
	left: 0;
}

#main-viewport.offset-right {
	left: var(--offset-right-sm);
	transition-duration: 0.8s;
	transition-delay: 0.3s;
}

#main-viewport.offset-up {
	bottom: var(--offset-up);
	transition-duration: 0.8s;
	transition-delay: 0.3s;
}

/* Over 961 wide */
@media only screen and (min-width: 961px) {
	#pioneer.offset-right::after {
		background-image: linear-gradient(to right, black var(--offset-right-lg), transparent calc(var(--offset-right-lg) + 20vw));
	}

	#main-viewport.offset-right {
		left: var(--offset-right-lg);
	}
}

/* Portrait mobile */
@media only screen and (max-width: 641px) and (orientation: portrait) {
	#pioneer::after {
		background-image: linear-gradient(to top, black var(--offset-up), transparent calc(var(--offset-up) + 25vh));
	}
}
