.toast-container {
	position: inherit;
	display: flex;
	gap: min(2vw, 0.7em);
	align-items: center;
	justify-content: space-between;
	grid-row: -2;
	grid-column-start: 1;
	background-color: var(--toast-bg-color);
	padding: 0.5em 0.5em 0.5em 0.8em;
	border-radius: 0.5em;
	transition-property: opacity, transform;
	transition-duration: 0.5s;
	transition-timing-function: var(--ease-out-bezier);
	opacity: 1;
	z-index: 40;
}

.toast-container.hidden {
	display: flex;
	transition-duration: 0.2s;
	opacity: 0;
	transform: translateY(2em);
}

.toast-container button.close {
	transform: scale(1.5);
	filter: invert();
}

.toast-content > div {
	display: flex;
	align-items: center;
	max-width: 30vw;
	gap: min(2vw, 0.7em);
}

.toast-content .content-text {
	display: block;
	color: var(--grayDark);
	font-size: clamp(0.75rem, min(2vh, 2vw), 0.9rem);
	font-weight: 600;
	min-width: 0;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}


.toast-content button.undo {
	color: rgb(0 37 158 / 80%);
	font-size: clamp(0.75rem, min(2vh, 2vw), 0.9rem);
	font-weight: 600;
	line-height: 1;
	border: 0.1em solid rgb(0 37 158 / 20%);
	border-radius: 0.25em;
	padding: 0.4em 0.6em;
	transition: transform 0.2s var(--ease-out-bezier);
	flex: 1;
}

.toast-content button.undo:hover {
	background-color: rgb(0 37 158 / 20%);
	transform: scale(1.05);
}


/* For mobile, put it at the top of the screen */
@media (max-width: 1025px) or (max-height: 600px) {
	.toast-container {
		grid-row: -4 /-3;
		justify-self: center;
		align-self: flex-end;
		grid-column: 2 / -2;
		margin: 0 0 3em;
	}

	body.offset-up .toast-container {
		grid-row: -5 / -4;
	}

	.toast-content > div {
		max-width: 66vw;
	}
}

@media (min-width: 1025px) {
	body.touch:has(nav.items-2) .toast-container {
		grid-row: 2;
		justify-self: center;
		grid-column: 2 / -2;
		margin: 0;
	}
}
