.os-scrollbar-handle:hover {
	cursor: pointer;
}
.os-scrollbar-handle:active {
	cursor: grabbing;
}

.icon.asteroid {
	width: 24px;
	height: 24px;
	background-image: url(./assets/svg/asteroid.svg);
	background-size: cover;
}

.icon.asteroid-watch-icon {
	background-image: url(./assets/svg/asteroid.svg);
	background-position: center;
	background-size: cover;
}

.icon.comet {
	width: 28px;
	height: 28px;
	background-image: url(./assets/svg/comet.svg);
	background-size: cover;
	transform: translate(-25%, -75%);
}

.icon-large.comet {
	width: 32px;
	height: 32px;
	transform: none;
}

.icon.spacecraft {
	width: 10px;
	height: 10px;
	background-image: url(./assets/svg/spacecraft.svg);
	background-size: cover;
}

.icon-large.spacecraft {
	width: 18px;
	height: 18px;
}

.icon.circle-arrow {
	width: 44px;
	height: 44px;
	background-image: url(./assets/svg/mobile_arrow.svg);
	background-size: cover;
}

.icon.prev-arrow {
	width: 23px;
	height: 15px;
	background-image: url(./assets/svg/arrow_prev.svg);
	background-size: cover;
}

.icon.next-arrow {
	width: 23px;
	height: 15px;
	background-image: url(./assets/svg/arrow_next.svg);
	background-size: cover;
}

.icon.rock {
	width: 80px;
	height: 80px;
	background-image: url(./assets/svg/rock.svg);
	background-size: cover;
}

.icon.ruler {
	width: 83px;
	height: 11px;
	background-image: url(./assets/svg/ruler.svg);
	background-size: cover;
}


/* Individual planet/moon dots */
.icon-circle-mercury {
	background-color: var(--mercury);
}
.icon-circle-venus {
	background-color: var(--venus);
}
.icon-circle-earth {
	background-color: var(--earth);
}
.icon-circle-mars {
	background-color: var(--mars);
}
.icon-circle-jupiter {
	background-color: var(--jupiter);
}
.icon-circle-saturn {
	background-color: var(--saturn);
}
.icon-circle-uranus {
	background-color: var(--uranus);
}
.icon-circle-neptune {
	background-color: var(--neptune);
}
.pioneer-label-div.moon .icon {
	background-color: var(--moon);
}

/* Story close icon */
.carousel-container .icon-close {
	margin: min(14px, 2.6vw) 0;
	transform: scale(1.2);
}
