.featured .os-padding {
	mask-image: linear-gradient(to bottom, transparent 30px, white 32px, white 70%, transparent 90%);
	mask-image: linear-gradient(to bottom, transparent 24px, white 36px, white 70%, transparent 90%);
}

.results .os-padding {
	mask-image: linear-gradient(to bottom, transparent 0, white 12px, white 80%, transparent 100%);
}

#search .os-scrollbar-vertical {
	right: 18px;
	top: 40px;
	bottom: 40px;
}
.search {
	padding-left: 3vw;
	z-index: 2;
	order: 3;
}

.search > div {
	position: absolute;
	left: 1rem;
	top: 0.7rem;
	right: 2rem;
}

.search .bar {
	position: relative;
	display: grid;
	transform-origin: right;
	width: calc(100% + 16px);
	padding: 0;
	background-color: var(--grayblueBackground);
}

.search input[type="text"] {
	font-weight: 100;
	font-size: 14px;
	color: var(--grayblueWhite);
}

.search input[type="text"]::placeholder {
	color: var(--grayblueText);
}

/* .search input[type="text"]:focus {
	outline: 3px solid #555555;
} */

.search-close input[type="text"] {
	opacity: 0;
}

.search-open input[type="text"] {
	opacity: 1;
	transition: opacity 1s 0.3s var(--ease-out-bezier);
}

.search .mag-open,
.search .mag-close {
	position: relative;
	margin: 0 0 0 auto;
	z-index: 102;
	display: block;
}

.icon-search {
	transform: scale(1.1);
	opacity: 0.75;
	transition: opacity 0.2s 0.1s ease-out, transform 0.2s 0.1s ease-out;
}

.bar .icon-search {
	opacity: 0;
}

.search .search-open {
	display: unset;
	z-index: 1;
}

.search .cover {
	width: unset;
	height: unset;
	inset: 0;
}

.search span.search-info {
	color: var(--grayblueDivider);
}

.search-close span.search-info {
	transition-duration: 0s;
}

.search .suggestion label {
	color: var(--grayblueMed);
}


.search-close .bar {
	width: 0;
	border-radius: 12px;
	opacity: 0;
	transform: scaleX(0);
	transition: transform 0.5s 0.1s var(--ease-out-bezier), opacity 0.5s 0.1s var(--ease-out-bezier);
}

.search-open .bar {
	border-radius: 12px 12px 0 0;
	opacity: 1;
	padding: 0.5rem;
	transform: scaleX(1);
	transition: transform 0.4s var(--ease-out-bezier), border-radius 0.2s ease-out;
}

.search-open .featured {
	background:
		linear-gradient(
			to bottom,
			var(--grayblueBackground) 64%,
			var(--grayblueDarkAlpha)
		);
	border-top: 1px solid var(--grayblueDivider);
}

.search .featured li {
	color: var(--grayblueMed);
	background-image: radial-gradient(at 10%, var(--grayblueDivider) 0%, transparent 110%);
}


.search .results {
	border-top: 1px solid var(--grayblueDivider);
	border-radius: 0 0 12px 12px;
	padding: 24px 0 48px;
	height: 300px;
	background:
		linear-gradient(
			to bottom,
			var(--grayblueBackground) 64%,
			var(--grayblueDarkAlpha)
		);
}

.search .result-div {
	margin: 9px 40px 9px 16px;
	padding: 8px 16px;
	border-radius: 6px;
	background-image: radial-gradient(at 10%, var(--grayblueDivider) 0%, transparent 110%);
	background-repeat: no-repeat;
	background-position: center;
	background-size: 0% 0%;
	color: var(--grayblueMed);
}

.search .result-div.active {
	background-color: transparent;
	background-size: 100% 100%;
	transition: background-size 0.15s ease-out;
	color: var(--grayblueWhite);
}

.search .featured li.active {
	color: var(--grayblueWhite);
}

.search .results .entries {
	padding: 1.5px 0;
	font-size: 14px;
	color: inherit;
}

.search .suggestion {
	margin-left: 20px;
}

@media only screen and (min-width: 641px) {
	.search > div {
		left: unset;
	}
}

@media only screen and (min-width: 961px) {
	.search {
		padding-left: 5vw;
	}
}

@media (pointer: fine) and (hover: hover) {
	.icon-search.mag-open:hover {
		opacity: 1;
		transform: scale(1.2);
		transition: opacity 0.3s ease-out, transform 0.3s ease-out;
	}

	.search .result-div:hover {
		background-color: transparent;
		background-size: 100% 100%;
		transition: background-size 0.15s ease-out;
		color: var(--grayblueWhite);
	}

	.search .featured li:hover {
		color: var(--grayblueWhite);
	}
}
