#asteroid-modals {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 1;
	pointer-events: all;
	z-index: 1000;
	transition: opacity 0.2s ease-in, top 0.4s ease-in-out;
}
#asteroid-modals.hidden {
	display: block !important;
	opacity: 0;
	pointer-events: none;
}

#asteroid-modals .asteroid-modals-backdrop {
	width: 100%;
	height: 100%;
	background: var(--black);
	opacity: 0.75;
}
#asteroid-modals .asteroid-modal {
	background:
		linear-gradient(
			to bottom,
			var(--grayblueBackground) 64%,
			var(--grayblueDarkAlpha)
		);
	border-radius: 12px;
	width: 500px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translateX(-50%) translateY(-50%);
	max-height: calc(100vh - 70px);
	overflow: hidden;
}

#asteroid-modals .asteroid-modal-header {
	display: flex;
	justify-content: space-between;
	height: 30px;
	line-height: 30px;
	padding: 0 18px;
	margin: 18px 0;
}
#asteroid-modals .asteroid-modal-svg {
	font-size: 29px;
	line-height: 34px;
}
#asteroid-modals .asteroid-modal-title {
	font-size: 26px;
	line-height: 34px;
	letter-spacing: 1px;
}
#asteroid-modals .asteroid-modal-close span {
	transform: scale(2.1);
	opacity: 0.5;
	transition: opacity 0.25s ease-in-out;
}

#asteroid-modals .asteroid-modal-collapse {
	display: none;
	width: 30px;
	height: 30px;
	padding: 4px;
	color: var(--grayblueWhite);
}

#asteroid-modals .asteroid-modal-content {
	max-height: calc(100vh - 136px);
	overflow-y: auto;
}

@media only screen and (max-width: 641px) {
	#asteroid-modals .asteroid-modal {
		width: calc(100% - 30px);
		background:
			linear-gradient(
				to bottom,
				var(--grayblueBackground) 64%,
				rgba(0, 0, 0, 0.8)
			);
		top: unset;
		bottom: 80px;
		left: 50%;
		transform: translateX(-50%);
	}
	#asteroid-modals .asteroid-modal-content {
		max-height: calc(100vh - 160px);
	}
	#asteroid-modals.hidden {
		top: 100vh;
	}
	#asteroid-modals .asteroid-modal-close {
		display: none;
	}
	#asteroid-modals .asteroid-modal-collapse {
		display: block;
	}
}

@media (pointer: fine) and (hover: hover) {
	#asteroid-modals .asteroid-modal-close span:hover {
		opacity: 0.8;
	}
}

