/* Carousel content */
.panel-content .asteroid-carousel {
	padding: 2em 0 4.2em;
	height: 250px;
}

/* Bullets */
.asteroid-carousel .bullet-container {
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
}


/* Slides */
.asteroid-carousel .swiper-slide {
	padding: 5px 0 0.1em;
	margin: 0 0 1.5em;
	mask-image: linear-gradient(to bottom, transparent 5px, white 10px, white 90%, transparent 100%);
}

.asteroid-carousel .swiper-slide h4 {
	font-size: 18px;
	letter-spacing: 0;
	color: var(--grayblueWhite);
	padding: 0.75em 0;
}

.asteroid-carousel .swiper-slide p {
	margin: 0;
	color: var(--grayblueMed);
	font-size: 14px;
	line-height: 1.6;
}

.asteroid-carousel .swiper-slide p.long {
	padding: 0 1.5em 1em 0;
}

.asteroid-carousel .swiper-slide .value {
	color: var(--grayblueLight);
	font-size: 16px;
	letter-spacing: 0;
	margin-top: 0.4em;
}

/* Anything over 961, we can incresed some font and icon sizes  slightly */
@media only screen and (min-width: 961px) {
	/* Carousel content */
	.panel-content .asteroid-carousel {
		height: 275px;
	}

	/* Text */
	.asteroid-carousel .swiper-slide h4 {
		font-size: 20px;
	}

	.asteroid-carousel .swiper-slide p {
		font-size: 18px;
		line-height: 1.8;
	}

	.asteroid-carousel .swiper-slide .value {
		font-size: 18px;
	}
}

/* Less than 641px and portrait */
@media only screen and (max-width: 641px) and (orientation: portrait) {
	/* Main padding */
	#panel-container.asteroid-panel .panel-main {
		padding: 12px 20px;
	}

	/* Carousel content */
	.panel-content .asteroid-carousel {
		height: 175px;
		padding: 1.8em 0 3em;
	}

	/* Slides */
	.asteroid-carousel .swiper-slide {
		mask-image: linear-gradient(to bottom, transparent 2px, white 6px, white 80%, transparent 100%);
	}

	/* Bullets */
	.asteroid-carousel .bullet-container {
		margin-top: 0.5em;
	}

	/* Adjust text */
	.asteroid-carousel .swiper-slide h4 {
		padding: 0 0 0.4em;
	}

	/* .asteroid-carousel .swiper-slide p.long {
		padding: 0.7em 1em 1em 0;
	} */

	.asteroid-carousel .swiper-slide .value {
		margin-top: 0.2em;
	}
}

/* Height-related queries */
@media only screen and (max-height: 641px) {
	/* Main padding */
	#panel-container.asteroid-panel .panel-main {
		padding: 16px 20px;
	}

	/* Carousel content */
	.panel-content .asteroid-carousel {
		height: min(220px, 50vh);
		padding: 1.8em 0 2.5em;
	}

	/* Bullets */
	.asteroid-carousel .bullet-container {
		margin: 0 auto 1.5em;
	}

	/* Text */
	.asteroid-carousel .swiper-slide h4 {
		font-size: 16px;
	}
	.asteroid-carousel .swiper-slide p {
		line-height: 1.4;
	}
}

