/* Splash Screen CSS */
.splash-screen {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: black;
	z-index: 9998;
}
.splash-screen-wrapper {
	animation: fade-in 0.3s ease-in;
}
.splash-screen-logo {
	position: absolute;
	left: 50%;
	top: 15%;
	transform: translateX(-50%) translateY(-50%);
	width: 110px;
	height: 110px;
	opacity: 0.7;
	background-size: contain;
	background-repeat: no-repeat;
	background-image: url(./assets/default/svg/nasa_logo.svg);
}
.splash-screen-asteroid {
	position: absolute;
	left: 50%;
	top: 44%;
	transform: translateX(-50%) translateY(-50%);
	width: 68vh;
	height: 68vh;
	background-size: contain;
	background-repeat: no-repeat;
	background-image: url(./assets/images/splash_bennu.png);
}
.splash-screen-text {
	position: absolute;
	left: 50%;
	top: 55%;
	transform: translateX(-50%) translateY(-50%);
	text-transform: uppercase;
	letter-spacing: 8px;
	font-weight: 300;
	text-align: center;
}
.splash-screen-text > div:nth-child(1) {
	font-size: 34px;
	line-height: 60px;
	padding-left: 8px;
	color: var(--grayblueLight);
}
.splash-screen-text > div:nth-child(2) {
	font-size: 24px;
	line-height: 40px;
	padding-left: 8px;
	color: var(--grayblueMed);
}
.splash-screen-text > div:nth-child(3) {
	font-size: 48px;
	line-height: 80px;
	font-weight: bold;
	letter-spacing: 42px;
	padding-left: 42px;
	color: var(--grayblueWhite);
}

.splash-screen-enter-background {
	content: "";
	position: absolute;
	left: 50%;
	top: calc(86% - 54px);
	background: radial-gradient(circle at center, rgba(255, 255, 255, 0.0), rgba(255, 255, 255, 0.03) 67%, rgba(255, 255, 255, 0.08) 70%, rgba(255, 255, 255, 0.09));
	width: 100vw;
	height: 100vw;
	border-radius: 50%;
	padding-top: 8px;
	transform: translateX(-50%);
}
.splash-screen-enter {
	position: absolute;
	left: 50%;
	top: calc(86% - 54px);
	height: calc(14% + 54px);
	width: 100vw;
	cursor: pointer;
	transform: translateX(-50%);
}
.splash-screen-swipe-arrow {
	display: flex;
	flex-flow: column;
	color: var(--grayblueMed);
}
.splash-screen-swipe-arrow::before {
	content: "Scroll to enter";
	font-size: 20px;
	opacity: 0.7;
	margin-top: 20px;
	line-height: 38px;
	font-weight: 200;
	text-align: center;
}
.splash-screen-swipe-arrow > svg {
	margin: auto;
	opacity: 0.8;
}

.splash-screen-stars1 {
	width: 3px;
	height: 3px;
	background: transparent;
	pointer-events: none;
}
.splash-screen-stars2 {
	width: 2px;
	height: 2px;
	background: transparent;
	pointer-events: none;
}

@keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@media only screen and (max-width: 960px) {
	.splash-screen-logo {
		width: 90px;
		height: 90px;
	}
	.splash-screen-text > div:nth-child(1) {
		font-size: 24px;
		line-height: 50px;
	}
	.splash-screen-text > div:nth-child(2) {
		font-size: 20px;
		line-height: 30px;
		padding-left: 8px;
	}
	.splash-screen-text > div:nth-child(3) {
		font-size: 36px;
		line-height: 70px;
		letter-spacing: 32px;
		padding-left: 32px;
	}
	.splash-screen-swipe-arrow::before {
		content: "Swipe to enter";
		font-size: 16px;
		line-height: 20px;
	}
	.splash-screen-swipe-arrow > svg {
		transform: rotateZ(180deg) scale(0.7);
	}
}
@media only screen and (max-width: 660px) {
	.splash-screen-logo {
		width: 80px;
		height: 80px;
	}
	.splash-screen-text > div:nth-child(1) {
		font-size: 20px;
		line-height: 40px;
	}
	.splash-screen-text > div:nth-child(2) {
		font-size: 16px;
		line-height: 20px;
	}
	.splash-screen-text > div:nth-child(3) {
		font-size: 26px;
		line-height: 50px;
		letter-spacing: 18px;
		padding-left: 18px;
	}
	.splash-screen-swipe-arrow::before {
		font-size: 14px;
		line-height: 20px;
	}
	.splash-screen-swipe-arrow > svg {
		transform: rotateZ(180deg) scale(0.6);
	}
}

@media only screen and (max-height: 550px) {
	.splash-screen-logo {
		width: 70px;
		height: 70px;
	}
	.splash-screen-text > div:nth-child(1) {
		font-size: 20px;
		line-height: 40px;
	}
	.splash-screen-text > div:nth-child(2) {
		font-size: 16px;
		line-height: 20px;
	}
	.splash-screen-text > div:nth-child(3) {
		font-size: 26px;
		line-height: 50px;
		letter-spacing: 18px;
		padding-left: 18px;
	}
	.splash-screen-swipe-arrow::before {
		font-size: 14px;
		line-height: 20px;
	}
	.splash-screen-swipe-arrow > svg {
		transform: rotateZ(180deg) scale(0.6);
	}
}
