/* Adding a minmax removes minWidth for grid items and prevents them from expanding the whole grid */
.simple-grid {
	grid-template-columns: repeat(12, minmax(0, 1fr));
	grid-template-rows: minmax(4.5rem, max-content) repeat(4, minmax(0, 1fr));
}


/* 480x320 phones and above */
@media only screen and (min-width: 320px) {
	/* Header */
	.grid-layout header.header {
		display: flex;
		height: unset;
		justify-content: space-between;
		gap: 0;
	}

	/* Controls */
	.grid-layout .float-right-bottom {
		grid-row: side;
		height: unset;
		margin-bottom: 50px;
		padding-bottom: 0;
	}

	/* Time */
	.grid-layout .float-mid-bottom {
		grid-template-columns: unset;
		grid-template-rows: 50% 50%;
		margin: 0;
		width: 100%;
		justify-items: stretch;
		bottom: 0;
	}
}

/* Landscape. 480x320 phones and above */
@media only screen and (min-width: 320px) and (orientation: landscape) {
	/* Controls */
	.grid-layout .float-right-bottom {
		grid-row: side;
		height: unset;
		margin-bottom: 20px;
		padding-bottom: 0;
		padding-right: 0;
	}

	/* Mid-bottom float */
	/* .asteroid-view .grid-layout .float-mid-bottom {
		bottom: 32px;
	} */
}

/* Landscape Tablets */
@media only screen and (min-width: 1025px) and (orientation: landscape) {
	.asteroid-view .grid-layout .float-mid-bottom {
		bottom: 0;
	}
}

/* Portrait Tablets */
@media only screen and (min-width: 641px) and (orientation: portrait) {
	/* Controls */
	.grid-layout .float-right-bottom {
		grid-row: side;
		height: unset;
		margin-bottom: 0;
		padding-bottom: 0;
		padding-right: 0;
	}
}

@media only screen and (min-width: 641px) {
	.grid-layout header.header {
		justify-content: flex-start;
		margin: 0;
	}
}

/* Exclude panorama. Big landscape tablets, laptops/desktops, and above */
@media only screen and (min-width: 1025px) and (min-height: 600px) {
	/* Time */
	.grid-layout .float-mid-bottom {
		grid-area: foot;
		grid-template-rows: unset;
		height: 100%;
		align-items: start;
	}
}

@media only screen and (max-height: 600px) {
	/* Main grid */
	.simple-grid {
		grid-template-rows: minmax(12vh, max-content) repeat(4, minmax(0, 1fr));
	}

	.grid-layout .float-mid-bottom {
		gap: 0;
	}

	.grid-layout header.header {
		margin: 0.25rem 0 0;
	}
}
