:root {
	/* Primary */
	--black: #000000;
	--blackAlpha: #00000380;
	--white: #ffffff;
	--whiteAlpha15: #ffffff26; /* 15% transparent */
	--gray: #95959d;
	--grayAlpha: #95959d33; /* 20% transparent */
	--grayMed: #c6c6c9;
	--grayLight: #eaeaeb;
	--grayDark: #252527;
	--grayDarkAlpha: #25252799; /* 60% transparent */
	--grayDarkAlpha80: #252527cc; /* 80% transparent */
	--cta-blue: #3e63dd;
	--toast-bg-color: #c6d4f9;
	--grayblueWhite: #f0f8fc;
	--grayblueLight: #d2e1e8;
	--grayblueMed: #a4bac1;
	--grayblueText: #83959b;
	--grayblueDivider: #25282b;
	--grayblueBackground: #151a1d;
	--grayblueButton: #425571;

	/* Semantic */
	--live: #47da84;
	--liveAlpha: #47da84bf; /* 75% transparent */
	--error: #d55033;
	--grayDivider: rgb(55 55 58);

	/* Gradients */
	--bgGradient: linear-gradient(180deg, #272527 0%, rgb(37 37 39 / 80%) 52.08%, rgb(37 37 39 / 0%) 100%);
	--mobileBgGradient: linear-gradient(180deg, #252527ee 0%, rgb(37 37 39 / 85%) 52.08%, rgb(0 0 0 / 80%) 100%);

	/* Secondary */
	--sun: #f7f4df;
	--sunMed: #b8b7a7;
	--sunDark: #626259;
	--mercury: #9768ac;
	--mercuryMed: #714e81;
	--mercuryDark: #3c2a45;
	--venus: #b07919;
	--venusMed: #845b13;
	--venusDark: #46300a;
	--earth: #0099cc;
	--earthMed: #007399;
	--earthDark: #003d52;
	--mars: #9a4e19;
	--marsMed: #733a13;
	--marsDark: #3d1f0a;
	--jupiter: #da8b72;
	--jupiterMed: #a36855;
	--jupiterDark: #57382e;
	--saturn: #d5c187;
	--saturnMed: #786d4c;
	--saturnDark: #554d36;
	--uranus: #68ccda;
	--uranusMed: #4e99a3;
	--uranusDark: #2a5257;
	--neptune: #708ce3;
	--neptuneMed: #5469aa;
	--neptuneDark: #2d385b;
	--nasa-blue: #288bff;

	/* Tertiary */
	--spacecraft: #cd9745;
	--moon: #b6acac;
	--dwarfPlanet: #929871;
	--comet: #7e86b0;
	--asteroid: #806262;
	--constellation: #59b2ff;
	--constellationGlow: #287bc4;
	--constellationGlowMed: #165d9b;
	--constellationGlowDark: #070a99;
}
