/*    TOGGLE SWITCH   */
/*
	<div class="eyes-toggle">
		<input type="checkbox" id="toggle" checked>
		<label for="toggle"></label>
	</div>
*/
.eyes-toggle {
	position: relative;
	height: 26px;
}
.eyes-toggle input[type="checkbox"] {
	appearance: none;
	cursor: pointer;
}
.eyes-toggle input[type="checkbox"]:focus {
	outline: 0;
}

.eyes-toggle input {
	height: 26px;
	width: 40px;
	border-radius: 16px;
	display: inline-block;
	position: relative;
	margin: 0;
	border: 1px solid #827a7a;
	background: linear-gradient(180deg, #2d2f39 0%, #1f2027 100%);
	transition: all 0.2s ease;
}
.eyes-toggle input::after {
	content: "";
	position: absolute;
	top: 2px;
	left: 2px;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	background: #827a7a;
	box-shadow: 0 1px 2px rgba(44, 44, 44, 0.2);
	transition: all 0.2s cubic-bezier(0.5, 0.1, 0.75, 1.35);
}
.eyes-toggle input:checked {
	border-color: #b6acac;
}
.eyes-toggle input:checked::after {
	transform: translateX(15px);
	background: #b6acac;
}
.eyes-toggle label {
	cursor: pointer;
	width: 26px;
	height: 26px;
	position: absolute;
	left: 0;
	top: 0;
	transition: all 0.2s ease;
}
.eyes-toggle label::after {
	border: 2px solid #252527;
	border-top: none;
	border-right: none;
	content: "";
	width: 6px;
	height: 3px;
	position: absolute;
	left: 10px;
	top: 10px;
	opacity: 0;
	transform: rotate(-45deg);
	transition: all 0.2s ease;
}
.eyes-toggle input[type="checkbox"]:checked + label {
	border-color: #b6acac;
	left: 14px;
}

.eyes-toggle input[type="checkbox"]:checked + label::after {
	opacity: 1;
}

/*    END TOGGLE SWITCH   */

/*    CHECKBOX   */
/*
  <div class="eyes-checkbox">
    <input type="checkbox" checked id="checkbox" />
    <label for="checkbox"></label>
  </div>
*/
.eyes-checkbox {
	position: relative;
	width: 22px;
	height: 22px;
}

.eyes-checkbox label {
	background-color: #252527;
	border: 1px solid #b6acac;
	cursor: pointer;
	width: 22px;
	height: 22px;
	position: absolute;
	left: 0;
	top: 0;
	border-radius: 3px;
	transition: all 0.2s ease;
}
@media (pointer: fine) and (hover: hover) {
	.eyes-checkbox label:hover {
		background-color: #3a3a3a;
	}
}

.eyes-checkbox label::after {
	border: 2px solid #252527;
	border-top: none;
	border-right: none;
	content: "";
	width: 9px;
	height: 4px;
	position: absolute;
	left: 5px;
	top: 6px;
	opacity: 0;
	transform: rotate(-45deg);
	transition: all 0.2s ease;
}

.eyes-checkbox input[type="checkbox"] {
	visibility: hidden;
}

.eyes-checkbox input[type="checkbox"]:checked + label {
	background-color: #b6acac;
	border-color: #b6acac;
}

.eyes-checkbox input[type="checkbox"]:checked + label::after {
	opacity: 1;
}

/*    END CHECKBOX    */
